.lk-order{
  margin-bottom: 2em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__back{
    height: 2.25em;
    padding: 0 .94em;
    text-transform: uppercase;
    white-space: nowrap;
    font-weight: 500;
    svg{
      width: .375em;
      height: auto;
      margin-right: .625em;
    }
    span{
      font-size: .75em;
      line-height: 130%;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__info{
    display: flex;
    align-items: center;
    .text14{
      color: #646464;
      b{
        color: #141414;
        font-weight: 500;
      }
    }
    .order-badge{
      margin-left: .625em;
    }
  }
  &__status{
    display: flex;
    align-items: center;
  }
}

.order-detail{
  margin-top: 2em;
  border: 1px solid #F1F1F1;
  &__top{
    padding: 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    .text18{
      font-weight: 700;
      line-height: 111%;
      color: #1F1E31;
    }
  }
}

.detail-item{
  display: flex;
  justify-content: space-between;
  padding: .94em 1.25em;
  border-top: 1px solid #F1F1F1;
  &__title{
    display: flex;
  }
  &__title2{
    color: #1F1E31;
    font-weight: 500;
  }
  &__title3{
    color: #494950;
    text-align: right;
    @media screen and (min-width: 993px){
      br{
        display: none;
      }
    }
  }
  &__title2,
  &__title3{
    font-size: 1em;
    line-height: 150%;
  }
  &__name{
    font-size: 1em;
    line-height: 162%;
  }
  &__count{
    margin-left: .56em;
    color: #7A7A7E;
    .text14{
      line-height: 129%;
    }
  }
  &__price{
    color: #000;
    white-space: nowrap;
    margin-left: .94em;
    .text17{
      line-height: 152%;
    }
    .text22{
      line-height: 100%;
    }
  }
  &__all{
    font-size: 1.5em;
    font-weight: 500;
  }
  &._type2{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
  }
  ._hide{
    display: none;
  }
}

.address-card{
  border: 1px solid #EAEAEA;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 3.9375em;
  }
  &__name{
    padding: 0 1.875em;
    color: #121214;
  }
  &__badge{
    height: 4em;
    padding: 0 1.44em;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 500;
    border-radius: 0;
    pointer-events: none;
    background: #FDF9F9;
    color: $primary;
    span{
      font-size: .8125em;
    }
    svg{
      width: 1.1875em;
      height: auto;
      margin-right: .625em;
    }
  }
  &__center{
    padding: .81em 1.875em 1em;
    min-height: 5.81em;
    display: flex;
    justify-content: space-between;
    color: #3B3B3E;
    .text15{
      line-height: 167%;
      font-weight: 500;
      letter-spacing: unset;
    }
    .text14{
      color: #87878D;
      line-height: 178%;
    }
    @media screen and (min-width: 993px){
      &._type2{
        max-width: 42em;
      }
    }
  }
  &__address{
    width: 17.625em;
  }
  &__nav{
    display: inline-flex;
    align-items: center;
    padding: 1.12em 1.875em;
    border: 1px solid #EAEAEA;
  }
  &__btn{
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 500;
    height: 1.4375em;
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    &:not(:last-child){
      &:after{
        content: '';
        flex-shrink: 0;
        width: 1px;
        height: 1.25em;
        border-right: 1px dashed #F1F1F1;
        margin: 0 1.875em;
        pointer-events: none;
      }
    }
    svg{
      height: 1.1875em;
      width: auto;
      margin-right: .56em;
      flex-shrink: 0;
    }
    span{
      font-size: 0.6875em;
      line-height: 209%;
    }
  }
  &__new{
    padding: .875em 1.875em 1.875em;
    display: flex;
  }
  &__add{
    display: flex;
    align-items: center;
    color: $primary;
    &:hover{
      color: $black;
    }
    &-icon{
      background: #F9F9F9;
      width: 3.875em;
      height: 3.875em;
      border-radius: 50%;
      flex-shrink: 0;
      @include center;
      margin-right: 1.06em;
      svg{
        width: 1em;
        height: auto;
      }
    }
    .text16{

    }
  }
}


@media screen and (max-width: 992px){
  .address-card{
    &__new{
      padding-top: 1.12em;
      justify-content: center;
    }
  }
}


@media screen and (max-width: 576px){
  .lk-order{
    margin-top: 1.875em;
    &__info{
      display: block;
      &>.text14{
        line-height: 215%;
      }
    }
    &__status{
      margin-top: .19em;
    }
  }

  .order-detail{
    margin-top: 1.56em;
    &__top{
      padding: 1.25em .94em;
    }
  }

  .detail-item{
    padding: .94em;
    &._mob-row{
      @include grid100gap(1.25em);
      .detail-item__title3{
        text-align: left;
        color: #494950;
      }
    }
    &__title{
      display: block;
    }
    &__name{
      display: inline;
    }
    &__count{
      display: inline;
      margin: 0;
    }
    ._hide{
      display: unset;
    }
  }

  .address-card{
    &__top{
      flex-direction: column;
    }
    &__badge{
      order: -1;
    }
    &__name{
      padding: .81em 0 0;
      text-align: center;
    }
    &__center{
      padding: 1.12em 1.875em 1.875em;
      @include grid100gap(1.44em);
    }
    &__nav{
      display: flex;
      width: 100%;
      justify-content: center;
    }
    &__btn{
      &:not(:last-child){
        &:after{
          margin: 0 .9375em;
          border: none;
          width: 0;
        }
      }
    }
  }
}
