.footer{
  padding: 3.125em 0 0;
  position: relative;
  &:before,
  &:after{
    content: '';
    position: absolute;
    top: 3.125em;
    width: calc(50% - 8.36em);
    height: 1px;
    border-top: 1px solid #F0EFED;
  }
  &:before{
    left: 0;
  }
  &:after{
    right: 0;
  }
  &__logo{
    top: 0;
    width: 16.72em;
    padding: 0 2.38em;
    //background-color: #fff;
  }
  &__title{
    color: #4E4742;
    font-weight: 500;
    margin-bottom: .5em;
    .text15{
      letter-spacing: 0.01875em;
      line-height: 133%;
    }
  }
  &__conf{
    text-decoration-line: underline;
    color: #77706B;
  }
  &__c{
    color: #46403B;
  }
  &__by{
    width: 8.75em;
    flex-shrink: 0;
    margin-left: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-top,
.footer-bottom{
  position: relative;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.footer-top{
  padding: 4.29em 0 3.125em;
}

.footer-info{
  max-width: 16.375em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__free-delivery{
    color: #796F68;
    padding-top: .125em;
    .text16{
      line-height: 150%;
      letter-spacing: 0.02em;
    }
  }
  &__phone{
    margin-top: 2.12em;
  }
  .phone-btn{
    margin: .31em 0 0;
    &:before{
      display: flex;
    }
  }
  &__email{
    margin-top: 1.94em;
    &-link{
      font-weight: 500;
      line-height: 133%;
      letter-spacing: 0.01875em;
      text-decoration-line: underline;
      color: #EA7275;
    }
  }
}

.footer-nav{
  &__show svg{
    display: none;
  }
  &__title{
    font-weight: 500;
    color: #000;
  }
  &__hidden{
    margin-top: 1em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    color: #46403B;
    @include lastM(0 0 1.06em);
    span{
      font-size: .9375em;
      line-height: 133%;
      letter-spacing: 0.01875em;
    }
  }
}

.footer-info2{
  &__bottom{
    margin-top: 1.875em;
  }
}

.footer-socials{
  &__desc{
    margin-top: .5em;
    color: #77706B;
  }
}

.socials{
  display: flex;
  &__item{
    @include center;
    width: 3.75em;
    height: 3.75em;
    border-radius: 50%;
    border: 1px solid #ECDED8;
    @include lastM(0 .625em 0 0);
    svg{
      width: 1.3125em;
      height: auto;
    }
    &:hover{
      border-color: $primary;
      background-color: $primary;
      path{
        fill: #fff;
      }
    }
  }
}

.footer-payment{
  margin-top: 1.875em;
  &__items{
    display: flex;
    align-items: center;
    max-width: 21.5em;
    margin-top: .81em;
  }
  &__item{
    display: flex;
    align-items: center;
    height: 1.125em;
    transition: all .3s;
    @include lastM(0 1.375em 0 0);
    &:not(:hover){
      opacity: 0.5;
    }
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.footer-bottom{
  border-top: 1px solid #F0EFED;
  padding: 1.25em 0;
  &__inner{
    align-items: center;
  }
}


@media screen and (max-width: 992px){
  .footer{
    &:before,
    &:after{
      width: calc(50% - 5.265em);
    }
    &__logo{
      width: 10.53em;
      padding: 0 .88em;
    }
    &__title{
      .text15{
        font-size: .875em;
        line-height: 143%;
      }
    }
    &__by{
      width: 8.125em;
    }
    &__c{
      line-height: 167%;
    }
  }

  .footer-top{
    padding: 2.98em 0 1.5em;
    &__inner{
      flex-wrap: wrap;
    }
  }

  .footer-info{
    max-width: 12.5625em;
    &__phone{
      margin-top: .625em;
    }
    &__email{
      margin-top: .94em;
    }
  }

  .footer-nav{
    &__title{
      font-size: 1em;
    }
    &__hidden{
      margin-top: .44em;
    }
    &__link{
      @include lastM(0 0 .44em);
      span{
        font-size: .8125em;
        line-height: 154%;
        letter-spacing: 0.01625em;
      }
    }
  }

  .footer-info2{
    width: 100%;
    margin-top: 1.5em;
    display: flex;
    justify-content: space-between;
    &__bottom{
      margin-top: .94em;
    }
  }

  .footer-payment,
  .footer-socials{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 19.6875em;
  }

  .footer-payment{
    justify-content: space-between;
    margin: 0;
  }

  .socials{
    &__item{
      width: 3.4375em;
      height: 3.4375em;
    }
  }

  .footer-bottom{
    padding: .94em 0;
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__by{
      margin: .94em 0 0;
    }
  }

  .footer-top{
    padding: 2.1em 0 1em;
    &__inner{
      display: block;
    }
  }

  .footer-info{
    align-items: center;
    text-align: center;
    margin: 0 auto 1.25em;
    max-width: 16.4375em;
    .phone-btn{
      &__title{
        display: block;
      }
    }
  }

  .footer-nav{
    margin: 0;
    &._active{
      .footer-nav__show svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
      border-bottom: 1px solid #F0EFED;
      display: flex;
      align-items: center;
      justify-content: space-between;
      min-height: 2.5em;
      padding: 0 .9375em;
      svg{
        transition: all .3s;
        display: block;
        width: .5em;
        height: auto;
      }
    }
    &__title{
      font-size: 1.125em;
    }
    &__hidden{
      margin: 0;
      padding: .9375em 0;
      display: none;
    }
    &__link{
      @include lastM(0 0 .81em);
      span{
        font-size: .875em;
        line-height: 143%;
        letter-spacing: 0.0175em;
      }
    }
  }

  .footer-info2{
    display: block;
    margin-top: .94em;
  }

  .footer-payment,
  .footer-socials{
    max-width: 100%;
  }

  .footer-payment{
    margin-top: .94em;
  }

  .footer-bottom{
    &__inner{
      flex-direction: column;
      text-align: center;
    }
  }
}
