.search{
  padding: 1.875em 0;
  .search-field{
    margin-top: 1.875em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3.125em;
    .m-btn{
      height: 3.875em;
    }
  }
}

.empty-hint{
  @include center;
  padding: 2.19em;
  background: #FCF6F6;
  color: $lightBlack;
  img{
    flex-shrink: 0;
    width: 1.875em;
    margin-right: 1.25em;
  }
  a{
    color: $primary;
    text-decoration-line: underline;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .search{
    padding-top: 3.81em;
    .search-field{
      margin-top: .94em;
    }
    &__nav{
      margin-top: 2.19em;
    }
  }

  .empty-hint{
    flex-direction: column;
    text-align: center;
    padding: 2.19em 7em;
    img{
      margin: 0 0 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .search{
    padding-top: 1.875em;
    .search-field{
      margin-top: 1.25em;
    }
  }

  .empty-hint{
    padding: 2.19em;
    .text15{
      letter-spacing: unset;
    }
  }
}
