.order-sum{
  &__items{
    @include grid100gap(1.69em);
  }
  &__props{
    @include grid100gap(1.09em);
  }
  &__pay-way{
    @include grid100gap(1em);
  }
  &__btn{
    ._view2{
      display: none;
    }
  }
}

.cart-item2{
  display: grid;
  grid-template-columns: 5.5625em calc(100% - 5.5625em - 1.125em);
  gap: 1.125em;
  align-items: flex-start;
  &__price,
  &__count{
    margin-top: .6em;
  }
}

.order{
  border: 1px solid #EAEAEA;
  padding-bottom: 1em;
}

.order-login{
  padding: 1.25em 2.5em;
  border-bottom: 1px solid #EAEAEA;
  &__show{
    display: flex;
    align-items: center;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    color: #383838;
    font-weight: 500;
  }
  &__btn{
    padding: 0 .87em;
    margin-left: .625em;
    height: 2.375em;
  }
  &__hidden{
    margin-top: 1.875em;
    max-width: 29em;
    display: none;
  }
}

.login-form{
  @include grid100gap(.94em);
  width: 29em;
  .m-check{
    display: inline-flex;
    margin: 0 auto;
    label{
      color: #000;
      span{
        font-size: .9375em;
      }
    }
  }
  .m-btn{
    padding: 0;
  }
}

.order-promo{
  padding: 1.56em 2.5em;
  &__inner{
    border: 1px dashed #B0AEA8;
    padding: 1.25em;
  }
  &__top{
    color: #383838;
    button{
      color: $primary;
      text-decoration: underline;
      &:hover{
        text-decoration: none;
      }
    }
  }
  &__bottom{
    margin-top: .625em;
    display: none;
  }
  .promo-code{
    max-width: 23.5625em;
  }
}

.delivery-type{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .75em));
  gap: 1.5em;
  padding: 0 2.5em;
  .m-radio{
    input{
      &:checked~label:after{
        border-color: $primary;
        border-width: 2px;
      }
    }
    label{
      padding: 1.25em 1.25em 1.25em 3.5em;
      &:before{
        top: 1.25em;
        left: 1.25em;
      }
      &:after{
        transition: all .3s;
        content: '';
        position: absolute;
        border: 1px solid #DFE2E4;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
      }
      span{
        margin-top: .4em;
        &:last-child{
          margin-top: 0;
        }
      }
      b{
        font-weight: 500;
        color: #62676F;
      }
    }
  }
  @media screen and (min-width: 577px){
    br{
      display: none;
    }
  }
}

.delivery-form{
  display: none;
  padding: 1.56em 2.5em;
  &._active{
    display: block;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .78em));
    gap: 1.56em;
  }
  ._full{
    grid-column: 1/-1;
  }
}

.delivery-time{
  display: flex;
  align-items: center;
  &__title{
    color: #000;
    letter-spacing: unset;
  }
  .date-input,
  .time-input{
    margin-left: 1.25em;
  }
}

.time-input,
.date-input{
  display: flex;
  height: 3.5625em;
  position: relative;
  &__title{
    background: #FCF7F7;
    padding: 0 .94em;
    height: 100%;
    display: flex;
    align-items: center;
    flex-shrink: 0;
  }
  input{
    width: 100%;
    flex-grow: 1;
    height: 100%;
    border: 1px solid #F8F5F5;
    background: #FFF;
    font-size: .9375em;
    padding: 0 1em;
    cursor: pointer;
  }
}

.date-input{
  width: 14em;
  &__icon{
    position: absolute;
    top: 50%;
    right: .94em;
    transform: translateY(-50%);
    width: 1.5em;
    pointer-events: none;
  }
  input{
    padding-right: 2.5em;
    @include sameNumWidth;
  }
}

.time-input{
  width: 14.2em;
  z-index: 3;
}




@media screen and (max-width: 992px){
  .order-page{
    .cart__inner{
      gap: 2.5em;
    }
  }

  .order-sum{
    padding: 1.125em;
    &__btn{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .cart-item2{
    @media screen and (min-width: 577px){
      align-items: center;
      &__info{
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      &__count{
        margin: 0 0 0 1em;
        flex-shrink: 0;
      }
    }
  }

  .order{
    padding-bottom: 1.25em;
  }

  .order-login{
    padding: .625em 1.25em;
  }

  .login-form{
    max-width: 24.6875em;
  }

  .order-promo{
    padding: 1.56em 1.25em 1.25em;
  }

  .delivery-type{
    padding: 0 1.25em;
    grid-template-columns: repeat(2,calc(50% - .47em));
    gap: .94em;
  }

  .delivery-form{
    padding: 1.25em;
    &__grid{
      row-gap: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .order-sum{
    padding: .5em;
  }

  .order-login{
    padding: .625em .94em;
  }

  .order-promo{
    padding: 1.56em .94em 1.25em;
  }

  .delivery-type{
    padding: 0 .94em;
    grid-template-columns: 100%;
  }

  .delivery-form{
    padding: 1.25em .94em;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .delivery-time{
    @include grid100gap(.875em);
    .date-input,
    .time-input{
      margin: 0;
      width: 100%;
    }
  }
}
