.blog{
  padding: 2.5em 0 3.125em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 23.81em) 22em;
    justify-content: space-between;
  }
  .pagination{
    margin-top: 4.37em;
  }
}

.subscribe{
  padding: 1.875em 1.5em;
  background: #FBF7F7;
  margin-top: 1.25em;
  &__top{
    @include grid100gap(.81em);
    text-align: center;
    .text21{
      color: #000;
      letter-spacing: 0.02625em;
      line-height: 96%;
    }
    .text14{
      color: #393939;
      line-height: 135%;
    }
  }
  &__wrap{
    @include grid100gap(1.37em);
    margin-top: .81em;
  }
  .fg input,
  .m-btn{
    height: 3.6875em;
  }
  .error-text{
    position: static;
  }
  .m-check{
    margin-top: 1.25em;
  }
}

.blog-nav{
  padding: 1.25em 1.5em;
  border: 1px solid #DFDFDF;
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    svg{
      display: none;
    }
  }
  &__hidden{
    margin-top: .625em;
    @media screen and (min-width: 992px){
      display: block !important;
    }
  }
  &__link{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .94em 0;
    &._active{
      color: $primary;
    }
    &:not(:last-child){
      border-bottom: 1px solid #DFDFDF;
    }
    &-title{
      text-transform: uppercase;
      font-weight: 500;
      span{
        font-size: .8125em;
        line-height: 115%;
      }
    }
    &-num{
      padding: 0 .5em;
      border: 1px solid #DFDFDF;
      height: 1.5625em;
      display: flex;
      align-items: center;
      color: #686868;
      font-weight: 500;
      span{
        font-size: .6875em;
        line-height: 136%;
      }
    }
  }
}

.news-card{
  &__img{
    display: block;
    overflow: hidden;
    transition: all .75s;
    @include imgCover(56%);
    &:hover{
      transform: scale(1.025);
    }
    &-wrap{
      margin-bottom: .94em;
      overflow: hidden;
    }
    img{
      transition: all .75s;
    }
    &:hover{
      img{
        transform: scale(1.05);
      }
    }
  }
  &__bottom{
    margin-top: .5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .text14{
    color: #44413F;
    line-height: 193%;
  }
}

.article{
  &__top{
    display: flex;
    align-items: center;
    margin-top: .625em;
  }
  &__badge{
    display: flex;
    align-items: center;
    padding: 0 .625em;
    height: 2.625em;
    background-color: $pinkLight;
    font-weight: 500;
    color: #fff;
    text-transform: uppercase;
    margin-right: .75em;
    span{
      font-size: 0.75em;
      line-height: 120%;
    }
  }
  &__date{
    display: flex;
    align-items: center;
    color: #44413F;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 0.375em;
      height: 0.375em;
      border-radius: 50%;
      background-color: $pinkLight;
      margin-right: .75em;
    }
  }
  .text{
    margin-top: 2em;
    color: $black;
  }
}

.blog-collection{
  margin-top: 6.25em;
}

.blog-slider{
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
    @media screen and (min-width: 993px){
      &-nav{
        display: flex;
        justify-content: center;
        margin-top: 3.125em;
      }
      &-button{
        position: static;
        margin: 0 .47em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding: 1.25em 0 2.5em;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.875em;
      position: relative;
    }
    .pagination{
      margin-top: 3.75em;
    }
  }

  .subscribe{
    margin-top: 1.875em;
    @media screen and (min-width: 577px){
      text-align: center;
      &__wrap{
        gap: 0;
        grid-template-columns: calc(100% - 16.435em) 16.435em;
      }
      .m-check{
        margin-left: auto;
        margin-right: auto;
        display: inline-flex;
      }
    }
  }

  .blog-nav{
    padding-top: 1.06em;
    padding-bottom: 1.06em;
    &._active{
      .blog-nav__show svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      svg{
        width: .5em;
        height: auto;
        display: block;
      }
    }
    &__hidden{
      display: none;
    }
  }

  .blog-item{
    .blog-nav{
      display: none;
    }
  }

  .article{
    .text{
      margin-top: 1.56em;
    }
  }

  .blog-collection{
    margin-top: 3.75em;
  }

  .blog-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    &__inner{
      gap: 1.25em;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
  }

  .subscribe{
    margin-top: 2.5em;
  }

  .blog-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}
