.catalog-item{
  padding: 3.75em 0;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 42.8125em - 3.125em) 42.8125em;
    gap: 3.125em;
    position: relative;
    min-height: 48.1875em;
  }
  .text43{
    max-width: 99%;
  }
  &__text,
  .product-card__reviews{
    margin-top: .94em;
  }
  &__text{
    .text15{
      line-height: 167%;
    }
  }
  &__count,
  &__nums{
    &-title{
      color: #4E4742;
      line-height: 111%;
      letter-spacing: 0.0225em;
      font-weight: 500;
    }
  }
  &__nums{
    margin-top: 1.69em;
  }
  .product-card__nums{
    margin-top: .94em;
  }
  .product-card__price,
  .product-card__price2{
    margin-right: .81em;
  }
  &__count{
    margin-top: 1.38em;
    display: flex;
    align-items: center;
    .cartcalc{
      margin-left: .94em;
    }
  }
  &__nav{
    margin-top: 1.37em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .47em));
    gap: .94em;
    max-width: 39.6875em;
  }
  &__advantages{
    display: grid;
    grid-template-columns: .9fr 1fr;
    gap: 1.875em 1em;
    margin-top: 3.125em;
    .advantage-item{
      margin: 0 !important;
    }
  }
  .gallery{
    position: absolute;
    left: 0;
    top: 0;
    width: calc(100% - 42.8125em - 3.125em);
  }
}

.gallery{
  display: grid;
  grid-template-columns: 6.1875em calc(100% - 6.1875em - 1.25em);
  gap: 1.25em;
  height: 48.1875em;
  .favourite-btn{
    position: absolute;
    top: 1.25em;
    right: 1.25em;
    z-index: 2;
  }
  &__img{
    display: block;
    @include imgCover(76.2%);
    padding: 0;
    img{
      transition: all .3s;
    }
  }
  &__small{
    position: relative;
    width: 100%;
    height: calc(100% + 1.25em);
    .swiper{
      &-slide{
        height: 20%;
        padding-bottom: 1.25em;
        position: relative;
        &:hover{
          cursor: pointer;
        }
        &:hover,
        &-thumb-active{
          .gallery__img:before{
            opacity: 1;
          }
        }
      }
      &-button{
        position: absolute;
        width: .9375em;
        z-index: 3;
        left: 50%;
        top: unset;
        bottom: 0;
        margin: 0;
        background-color: transparent;
        svg{
          width: 100%;
          height: auto;
        }
        &:hover{
          background-color: transparent;
          path{
            fill: #2F343A;
            opacity: 1;
          }
        }
      }
    }
    .gallery__img{
      padding: 0;
      height: 100%;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 3px solid rgba($black,.7);
        z-index: 2;
        transition: all .3s;
        opacity: 0;
      }
    }
  }
  &__big{
    width: 100%;
    position: relative;
    .gallery__img{
      padding: 0;
      height: 48.1875em;
    }
  }
}

.cartcalc{
  width: 8.1875rem;
  display: grid;
  grid-template-columns: 2.1875em calc(100% - 2.1875em * 2) 2.1875em;
  border: 1px solid #ECEFF4;
  button,
  input{
    height: 4.0625em;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #ECEFF4;
    border-right: 1px solid #ECEFF4;
    color: #323638;
    font-size: .875em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: rgba($primary,.2);
      path{
        fill: $primary;
      }
    }
  }
}

.product-detail{
  padding-bottom: 3.125em;
  &__top{
    border-bottom: 1px solid #DFDFDF;
  }
  &__content{
    margin-top: 2.5em;
  }
}

.tabs{
  display: flex;
}

.tab{
  height: 4.625em;
  display: flex;
  align-items: center;
  font-weight: 600;
  color: rgba($black,.6);
  transition: all .3s;
  cursor: pointer;
  position: relative;
  @include lastM(0 3.125em 0 0);
  &:after{
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 1px;
    background: $black;
    transition: inherit;
    opacity: 0;
  }
  span{
    font-size: 1.375em;
    letter-spacing: 0.0275em;
  }
  &._active{
    pointer-events: none;
    &:after{
      opacity: 1;
    }
  }
  &._active,
  &:hover{
    color: $black;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.product-desc{
  max-width: 72.625em;
  .text{
    color: #373637;


  }
}

.product-reviews{
  display: grid;
  grid-template-columns: calc(100% - 39.375em - 5.31em) 39.375em;
  justify-content: space-between;
  &__list{
    @include grid100gap(1.875em)
  }
}

.review-card2{
  border-bottom: 1px solid #D8D8D8;
  padding-bottom: 1.56em;
  &__info{
    margin-top: .69em;
    display: flex;
    align-items: center;
  }
  .rating{
    margin-right: .56em;
  }
  &__date{
    color: #656670;
  }
  &__text{
    margin-top: .69em;
    .text16{
      line-height: 167%;
    }
  }
  &__images{
    display: flex;
    flex-wrap: wrap;
    margin-top: .625em;
  }
  &__img{
    margin: .625em .44em 0 0;
    width: 10.8em;
    flex-shrink: 0;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
}

.review-form{
  &__title{
    text-align: center;
    color: #2C2F30;
  }
  &__content{
    margin-top: 2em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 2.125em 1.25em;
  }
  ._full{
    grid-column: 1/-1;
  }
  &__nav{
    margin-top: .38em;
    display: flex;
    justify-content: center;
  }
  .rating-input{
    &__item{
      width: 1.0625em;
    }
  }
}

.rating-field{
  @include center;
  .rating{
    margin-left: .44em;
  }
  .text14{
    color: #545454;
  }
}

.product-delivery{
  max-width: 65em;
}

.form-success{
  margin-top: 1.25em;
  min-height: 44em;
  padding: 4em;
  background: #FFF;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.05), 0px 10px 22px 0px rgba(0, 0, 0, 0.05), 0px 39px 39px 0px rgba(0, 0, 0, 0.04), 0px 89px 53px 0px rgba(0, 0, 0, 0.03), 0px 158px 63px 0px rgba(0, 0, 0, 0.01), 0px 246px 69px 0px rgba(0, 0, 0, 0.00);
  @include center;
  flex-direction: column;
  text-align: center;
  color: #414042;
  &__img{
    width: 4.25em;
  }
  &__text{
    margin-top: 1.875em;
    @include grid100gap(1em);
  }
  .text16{
    line-height: 169%;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-bottom: 0;
    padding-top: 1.25em;
    &__inner{
      grid-template-columns: 20.625em calc(100% - 20.625em - 1.25em);
      gap: 1.25em;
      padding-bottom: 12.5em;
      position: relative;
      min-height: unset;
    }
    &__info{
      min-height: 37.625em;
    }
    .product-card__reviews{
      margin-top: .75em;
    }
    &__text{
      margin-top: .75em;
    }
    &__nums{
      margin-top: 1.25em;
    }
    &__count{
      margin-top: 1em;
    }
    &__count,
    &__nums{
      &-title{
        font-size: .9375em;
        line-height: 133%;
        letter-spacing: 0.01875em;
      }
    }
    &__nav{
      grid-template-columns: 100%;
    }
    &__advantages{
      @media screen and (min-width: 577px){
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        padding: 1.875em 0;
        margin: 0;
      }
    }
    .gallery{
      width: 20.625em;
    }
  }

  .gallery{
    grid-template-columns: 100%;
    gap: .94em;
    height: unset;
    .favourite-btn{
      top: .625em;
      right: .625em;
    }
    &__small{
      height: auto;
      width: calc(100% + .88em);
      order: 2;
      .swiper{
        &-slide{
          width: calc(100% / 3);
          height: auto;
          padding-right: .88em;
          padding-bottom: 0;
        }
      }
    }
    .gallery__img{
      padding-bottom: 136%;
      height: unset !important;
    }
  }

  .product-detail{
    padding: .625em 0 1.875em;
    &__content{
      margin-top: 1.25em;
    }
  }

  .tab{
    @include lastM(0 3em 0 0);
    span{
      font-size: 1.0625em;
    }
  }

  .product-reviews{
    grid-template-columns: 100%;
    gap: 3.5em;
    &__list{
      gap: 1.25em;
    }
  }

  .review-card2{
    &__img{
      width: 8.3em;
    }
  }

  .review-form{
    &__content{
      margin-top: 1.25em;
    }
    &__grid{
      row-gap: .94em;
    }
  }

  .form-success{
    min-height: 38.8125em;
    padding: 6.75em;
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    padding: 1.25em 0 0;
    &__inner{
      padding: 0;
      grid-template-columns: 100%;
    }
    &__advantages{
      grid-template-columns: 100%;
      gap: 1.94em;
      padding: 1.875em 0;
      margin-top: .625em;
    }
    &__media{
      display: none;
    }
    .gallery{
      margin-top: .69em;
      width: 100%;
      position: relative;
      top: unset;
    }
    &__text{
      margin-top: 1.25em;
    }
  }

  .review-form{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .form-success{
    padding: .5em;
  }

  .review-card2{
    &__images{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .22em));
      gap: .625em .44em;
      margin-top: 1.25em;
    }
    &__img{
      margin: 0;
    }
  }
}
