.lk{
  padding: 2.19em 0 4em;
  &__grid{
    display: grid;
    grid-template-columns: 20.5em calc(100% - 20.5em - 1.875em);
    justify-content: space-between;
  }
  &__title{
    margin-bottom: 2.31em;
    font-weight: 500;
  }
  &__subtitle{
    margin-bottom: 2em;
  }
  &._hide-buttons-desktop{
    @media screen and (min-width: 993px){
      .lk-buttons{
        display: none;
      }
    }
  }
  .pagination{

  }
}

.lk-nav{
  overflow: hidden;
  background: #FFF;
  border: 1px solid #F0EFED;
}

.lk-link{
  min-height: 4.6875em;
  display: flex;
  align-items: center;
  padding: 1em 1.875em;
  border-bottom: 1px solid #EDF0F2;
  color: #232323;
  &._active,
  &:hover{
    background-color: $primary;
    border-color: $primary;
    color: #fff;
    path{
      stroke: #fff;
    }
  }
  &:last-child{
    border-bottom: none;
  }
  &__icon{
    width: 1.4375em;
    flex-shrink: 0;
    margin-right: .625em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
  }
  &__count{
    position: static;
    margin-left: auto;
    transform: unset;
    width: 1.3125em;
    height: 1.3125em;
  }
}

.lk-toggle-menu{
  display: none;
}

.lk-menu{
  &__close,
  &__bg{
    display: none;
  }
}

.lk-btn{
  position: relative;
  padding: 1.25em .25em;
  border: 1px solid #EAEAEA;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    height: 4.0625em;
    flex-shrink: 0;
    display: block;
    margin-bottom: .75em;
    svg{
      height: 100%;
      width: auto;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 118%;
    font-weight: 500;
  }
  &:hover,
  &._active{
    background: $primary;
    border-color: $primary;
    color: #fff;
    path{
      stroke: #fff;
    }
  }
}

.orders{
  margin-top: 3.125em;
  &._mt0{
    margin-top: 0;
  }
}

.orders-table{
  table{
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
  }
  td,
  th{

    border-bottom: 1px solid #EAEAEA;
    &:first-child{
      border-left: 1px solid #EAEAEA;
    }
    &:last-child{
      border-right: 1px solid #EDF0F2;
    }
  }
  th{
    border-top: 1px solid #EAEAEA;
    color: #45484A;
    font-weight: 700;
    text-align: left;
    span{
      font-size: 1.125em;
      line-height: 111%;
    }
  }
}

.orders-table{
  td,th{
    padding: 1.25em;
    &:nth-child(4){
      width: 8.5em;
    }
    &:last-child{
      width: 4.375em;
      padding-left: 0;
    }
  }
}

.order-item{
  &__link{
    width: 3.125em;
    height: 3.125em;
    border: 1px solid #EAEAEA;
    padding: 0;
    span{
      display: none;
    }
    svg{
      width: .3125em;
      height: auto;
    }
    &:hover{
      background-color: $primary;
      border-color: $primary;
      path{
        stroke: #fff;
      }
    }
  }
}

.order-badge{
  display: inline-flex;
  align-items: center;
  height: 2.125em;
  min-width: 6.25em;
  padding: 0 .75em;
  color: #FF6915;
  text-transform: uppercase;
  font-weight: 500;
  border: 1px solid #FF6915;
  &._green{
    color: #11A342;
    border-color: #11A342;
    &:before{
      background-color: #11A342;
    }
  }
  &._red{
    color: #F81D2A;
    border-color: #F81D2A;
    &:before{
      background-color: #F81D2A;
    }
  }
  &:before{
    content: '';
    flex-shrink: 0;
    width: 0.3125em;
    height: 0.3125em;
    border-radius: 50%;
    margin-right: .56em;
    background-color: #FF6915;
  }
  span{
    font-size: .6875em;
    line-height: 120%;
  }
}

.account-form{
  padding: 1.56em;
  border: 1px solid #EAEAEA;
  width: 65.125em;
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .78em));
  gap: 1.56em;
  &._pass{
    grid-template-columns: 100%;
    width: 35.3125em;
  }
  &._address{
    width: 64.625em;
    gap: 1.56em;
    @media screen and (min-width: 577px){
      grid-template-columns: repeat(2,calc(50% - .78em));
    }
    .account-form__btn{
      display: inline-flex;
      padding: 0 2em;
      width: auto;
      margin-right: auto;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
  ._full{
    grid-column: 1/-1;
  }
}

.success-message{
  color: #07961E;
  padding: .625em 0;
  @include center;
  svg{
    width: 1.0625em;
    height: auto;
    margin-right: .625em;
  }
  .text15{
    letter-spacing: unset;
  }
}


@media screen and (max-width: 992px){
  ._tablet-center{
    text-align: center;
  }

  .lk{
    padding: 1.25em 0 2.5em;
    &._hide-buttons-desktop{
      .lk-buttons{
        margin-bottom: 1.94em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__title,
    &__subtitle{
      margin-bottom: 1.25em;
    }
  }

  .lk-menu{
    @include menu;
    &._active{
      transform: translateX(0);
      &~.lk-menu__bg,
      &~.lk-menu__close{
        display: block;
      }
    }
  }

  .lk-nav{
    border: none;
  }

  .orders{
    margin-top: 2.5em;
    .empty-hint{
      padding: 2.19em 1.75em;
      .text15{
        line-height: 180%;
      }
    }
  }

  .account-form{
    padding: 1.25em;
    &._pass{
      width: 28.875em;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }
}



@media screen and (max-width: 576px){
  .lk{
    &._hide-buttons-mob{
      .lk-buttons{
        display: none;
      }
    }
  }

  .lk-toggle-menu{
    margin-bottom: 1.875em;
    @include center;
    border: 1px solid #EAEAEA;
    height: 3.5625em;
    width: 100%;
    color: #3B3C3D;
    font-weight: 600;
    text-transform: uppercase;
    span{
      font-size: .8125em;
      line-height: 147%;
    }
    svg{
      width: 0.75em;
      height: auto;
      margin-right: .625em;
    }
  }

  .lk-menu{
    width: calc(100% - 2.56em);
  }

  .lk-buttons{
    grid-template-columns: 100%;
  }

  .lk-btn{
    padding: .94em;
  }

  .orders-table{
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      @include grid100gap(.94em);
    }
    tr{
      border: 1px solid #EAEAEA;
      @include grid100gap(.3125em);
      padding: .94em;
    }
    td{
      border: none !important;
      padding: 0;
      width: 100% !important;
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: #1F1E31;
      &[data-title]{
        min-height: 1.875em;
        &:before{
          content: attr(data-title);
          font-size: .875em;
        }
      }
    }
    .text16{
      font-size: .8125em;
      font-weight: 500;
    }
  }

  .order-item{
    &__link{
      width: 100%;
      height: 2.375em;
      text-transform: uppercase;
      color: $primary;
      svg{
        margin-left: .25em;
      }
      path{
        fill: $primary;
      }
      span{
        display: block;
        font-size: .75em;
      }
      &:hover{
        color: #fff;
      }
    }
    &__price{
      font-size: 1.0625em;
    }
  }

  .account-form{
    grid-template-columns: 100%;
  }
}
