.about2{
  margin: 0;
  @media screen and (min-width: 993px){
    .about__inner{
      grid-template-columns: 26em 26em;
    }
    .about__leaf{
      left: 7.94em;
      right: unset;
    }
  }
}

.about3{
  padding: 2.19em 0 5.44em;
  .text{
    max-width: 54.5em;
  }
  &__flowers{
    position: absolute;
    top: 12.4em;
    right: 0;
    width: 48.75em;
  }
}


@media screen and (max-width: 992px){
  .about2{
    .about__leaf{
      display: none;
    }
    @media screen and (min-width: 577px){
      .about__inner{
        margin-top: 1.875em;
        grid-template-columns: 19em 19em;
      }
      .about__items{
        padding: 0 0 2.25em;
        position: relative;
        z-index: 2;
      }
      .about__img{
        position: absolute;
        width: 21.25em;
        margin: 0;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        overflow: unset;
        &:before{
          height: calc(100% - 3.75em);
          width: 15.75em;
        }
        img{
          height: auto;
          width: 100%;
        }
      }
      .about-item{
        @include lastM(0 0 .94em);
        .text15{
          letter-spacing: 0.02625em;
        }
        .text14{
          letter-spacing: 0.01625em;
        }
      }
      .about__petal{
        width: 1.875em;
        right: 25.94em;
        top: 11.69em;
      }
      .about__petal2{
        width: 2.75em;
        left: 7.31em;
        right: unset;
        top: unset;
        bottom: 1.69em;
      }
    }
  }

  .about3{
    padding: .625em 0 2.5em;
    &__flowers{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .about2{
    .about__inner{
      gap: .94em;
      margin-top: 1.5em;
    }
    .about__items{
      padding: 0;
    }
  }

  .about3{
    padding-top: 1.25em;
  }
}
