.rating{
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: .875em;
    &:not(:last-child){
      margin-right: .13em;
    }
    svg{
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: $black;
      }
      &~.rating-input__item {
        path{
          fill: $black;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: $black;
  }
}

.fg {
  position: relative;
  margin: 0;
  z-index: 2;
  textarea,
  input ,
  .nice-select{
    border: $border;
    padding: 0 1.25em;
    height: 3.9375em;
    width: 100%;
    font-size: 1em;
    background-color: #fff;
    color: $black;
    &::placeholder {
      color: #C1C1C1;
    }
    &:focus {
      border-color: $primary;
    }
    &._hide-placeholder{
      &~.fake-placeholder{
        opacity: 0;
      }
    }
  }
  textarea{
    height: 6.5625em;
    resize: vertical;
    padding-top: 1.125em;
    padding-bottom: 1.125em;
    resize: none;
  }
  label{
    transition: all .2s;
    font-size: .9375em;
    line-height: 157%;
    display: block;
    margin-bottom: .2em;
    color: #000;
    span{
      color: #FF1515;
    }
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0;
    white-space: nowrap;
    &:focus{
      border: $border;
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .75em;
      height: .75em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      margin-top: -.25em;
      border-radius: 0;
      width: calc(100% + 2px);
      left: -1px;
      border: $border;
      background: #FFF;
      box-shadow: none;
      max-height: 12em;
      overflow-y: auto;
      z-index: 4;
      @include scroll;
    }
    .option{
      width: calc(100% + 2px);
      height: 3.9375em;
      font-size: 1em;
      line-height: 157%;
      padding: 0 1.25em;
      display: flex;
      align-items: center;
      color: #2B2B2B;
      border: none;
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $primary;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  ._error-text{
    display: none;
  }
  &._error{
    input,
    textarea{
      border-color: #FF1515;
      &::placeholder{
        color: #FF1515;
      }
    }
    ._error-text{
      display: block;
      margin-top: .19em;
      color: #FF1515;
      span{
        font-size: 0.8125em;
      }
    }
  }
}

.fake-placeholder{
  position: absolute;
  top: 50%;
  left: 1.25em;
  transform: translateY(-50%);
  color: #959090;
  pointer-events: none;
  ._view2{
    display: none;
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1.375em;
  right: 1.25em;
  width: 1.125em;
  height: 1.125em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    path{
      stroke: $primary;
    }
    g{
      opacity: 1;
    }
  }
}

.m-radio,
.file-input,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    color: #78736F;
    span{
      font-size: .875em;
      line-height: 157%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
      background-color: #fff;
    }
    a{
      text-decoration: underline;
      font-size: .9375em;
      position: relative;
      z-index: 2;
    }
  }
  &._gray{
    label{
      color: #6B6E72;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    padding-left: 2.1875em;
    min-height: 1.375em;
    color: #363839;
    &:before {
      border: $border;
      width: 1.375em;
      height: 1.375em;
      @include bgImgSize(0);
      background-image: url(../img/check.svg);
      transition: 0;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    color: #2C333D;
    padding-left: 2.25em;
    min-height: 1.375em;
    font-size: 1em;
    line-height: 137.5%;
    &:before{
      width: 1.3125em;
      height: 1.3125em;
      border: $border;
      border-radius: 50%;
      background-image: url(../img/radio2.svg);
      @include bgImgSize(0%);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.file-input{
  &._uploaded{
    .file-input__inner{
      display: none;
    }
    .file-input__plus{
      display: block;
    }
  }
  input:hover{
    &~.file-input__wrap{
      border-color: $primary;
    }
  }
  &__wrap{
    background-color: #fff;
    padding: .875em;
    border-radius: 0.4375em;
    border: 1px dashed #D3D3D3;
    position: relative;
    min-height: 8.625em;
  }
  &__inner{
    padding: inherit;
    width: 100%;
    position: absolute;
    @include centerAbsolute;
    color: #838383;
    text-align: center;
    &>img{
      width: 1.125em;
      display: block;
      margin: 0 auto 1.12em;
    }
  }
  &__upload{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 1.42em * 4 / 5));
    gap: 1.42em;
    background-color: #fff;
  }
  &__plus{
    position: relative;
    display: none;
    order: 10;
    &-btn{
      padding-bottom: 100%;
      position: relative;
      background: #F8F7F7;
      display: block;
      width: 100%;
      &:hover{
        background-color: rgba($primary,.2);
      }
      svg{
        @include centerAbsolute;
        width: 1.3125em;
        height: auto;
      }
    }
  }
}

.upload-file{
  position: relative;
  z-index: 3;
  &__remove{
    position: absolute;
    top: -.31em;
    right: -.31em;
    width: 0.8125em;
    height: 0.8125em;
    @include center;
    &:hover{
      opacity: .8;
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__img{
    @include imgCover(100%);
  }
}


@media screen and (max-width: 992px){
  .rating-input{
    &__item{
      width: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .file-input{
    &__upload{
      grid-template-columns: repeat(3,calc(100% / 3 - .88em / 3));
    }
  }

  .fake-placeholder{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}
