.header__logo,
.footer__logo{
  display: block;
  width: 11.97em;
  @include centerAbsolute;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  @media screen and (min-width: 577px){
    &._open-menu{
      .catalog-menu{
        transform: translateX(0);
      }
      .menu__close,
      .menu__bg{
        display: flex;
      }
    }
  }
}

.header-top{
  padding: 1.75em 0;
  &__inner{
    display: flex;
    align-items: center;
    position: relative;
  }
}

.phone-btn,
.catalog-btn{
  display: flex;
  align-items: center;
  color: #3B353D;
  font-weight: 700;
  text-transform: uppercase;
  &__icon{
    transition: all .3s;
    width: 2.625em;
    height: 2.5625em;
    @include center;
    flex-shrink: 0;
    border-radius: 50%;
    background-color: #F4F5F6;
    margin-right: .875em;
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  &__title{
    letter-spacing: 0.01625em;
  }
  &:hover{
    color: $primary;
    .phone-btn__icon,
    .catalog-btn__icon{
      background-color: rgba($primary,5%);
      path{
        stroke: #fff;
      }
    }
  }
}

.phone-btn{
  margin-left: 2.5em;
  font-weight: 600;
  &:before{
    content: '';
    flex-shrink: 0;
    width: 0.3125em;
    height: 0.3125em;
    border-radius: 50%;
    background-color: $pinkLight;
    margin-right: .625em;
  }
  &__icon{
    display: none;
  }
  &__title{
    letter-spacing: 0.02em;
  }
}

.header-action{
  display: flex;
  align-items: center;
  margin-left: auto;
  &__btn{
    position: relative;
    width: 1.625em;
    flex-shrink: 0;
    @include center;
    @include lastM(0 1.56em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

._pin{
  position: absolute;
  width: .9375em;
  height: .9375em;
  background-color: $pinkLight;
  border-radius: 50%;
  font-weight: 500;
  color: #fff;
  top: -.25em;
  right: -.25em;
  @include center;
  span{
    letter-spacing: 0.0125em;
    font-size: .625em;
    line-height: 110%;
  }
}

.header-bottom{
  padding: .625em 0;
  border-top: 1px solid #F0EFED;
  border-bottom: 1px solid #F0EFED;
}

.nav__link,
.catalog-menu__item{
  font-weight: 700;
  color: #2D2B29;
  text-transform: uppercase;
  span{
    font-size: .75em;
    line-height: 275%;
    letter-spacing: 0.0525em;
  }
}

.nav{
  display: flex;
  justify-content: center;
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    @include lastM(0 4.06em 0 0);
  }
}

.close-btn{
  position: fixed;
  width: 1.125em;
  z-index: 21;
  left: 22.87em;
  top: 1.12em;
  &:hover{
    path{
      opacity: 1;
    }
  }
  svg{
    width: 100%;
    height: auto;
  }
}

.menu{
  &__bg,
  &__close{
    display: none;
    &._hide-desktop{
      display: none !important;
    }
  }
}

.catalog-menu{
  @media screen and (min-width: 577px){
    @include menu;
  }
  &__item{
    padding: 1.12em 2em;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #F6F5F4;
    &:first-child{
      border-top: 1px solid #F6F5F4;
    }
    &._active,
    &:hover{
      background-color: $primary;
      color: #fff;
      path{
        stroke: #fff;
      }
    }
    svg{
      width: 2.5625em;
      height: auto;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
}

.menu-group{
  &__show{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      width: 8.78em;
    }
  }

  .header-top{
    padding: .94em 0;
  }

  .nav{
    display: block;
    &__links{
      justify-content: space-between;
    }
    &__link{
      @include lastM(0);
    }
  }

  .catalog-btn,
  .phone-btn{
    &__icon{
      width: 2.1875em;
      height: 2.1875em;
      svg{
        width: 1.25em;
      }
    }
  }

  .catalog-btn{
    &__icon{
      margin: 0;
    }
    &__title{
      display: none;
    }
  }

  .phone-btn{
    margin-left: 2.19em;
  }

  .header-action{
    &__btn{
      width: 1.375em;
    }
  }

  .close-btn{
    width: .8125em;
    top: .94em;
  }
}


@media screen and (max-width: 576px){
  .header{
    &._open-menu{
      .menu{
        transform: translateX(0);
      }
      .menu__close,
      .menu__bg{
        display: flex;
      }
    }
  }

  .header-top{
    padding: 0 0 2.875em;
    position: relative;
    &__inner{
      justify-content: space-between;
      padding: .94em 0;
    }
  }

  .header-bottom{
    padding: 0;
    border: none;
  }

  .menu{
    @include menu;
    width: calc(100% - 2.56em);
    &__close{

    }
  }

  .close-btn{
    left: unset;
    right: .94em;
  }

  .header-action{
    position: absolute;
    left: -.9375em;
    top: 100%;
    width: calc(100% + 1.875em);
    padding: 0 .9375em;
    border-top: 1px solid #F0EFED;
    border-bottom: 1px solid #F0EFED;
    height: 2.875em;
    justify-content: space-between;
    &__btn{
      width: 1.625em;
    }
  }

  .phone-btn{
    &:before{
      display: none;
    }
    &__icon{
      margin: 0;
      display: flex;
    }
    &__title{
      display: none;
    }
  }

  .catalog-menu{
    &__item{
      padding: 1.12em 1.25em;
      &:first-child{
        border-top: none;
      }
    }
  }

  .menu-group{

    &._active{
      .menu-group__show svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      border-bottom: 1px solid #F6F5F4;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: .625em .9375em;
      svg{
        width: .5em;
        height: auto;
      }
    }
  }

  .nav{
    &__links{
      padding: .625em 1.56em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      @include lastM(0 0 .31em);
    }
  }
}
