.blog-reviews{
  .text21{
    color: #000;
  }
  &__items{
    margin-top: 1.56em;
    @include grid100gap(1.56em);
  }
  @media screen and (min-width: 993px){
    .review-card{
      &__top{
        display: block;
      }
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }
  &__nav{
    margin-top: 1.56em;
    display: flex;
    justify-content: center;
  }
}

.pay-info,
.delivery-info{
  @include lastM(0 0 2.2em);
  h4{
    margin-bottom: 1em;
  }
  .text{
    color: $black;
  }
}

.pay-info{
  .footer-payment__items{
    max-width: 28em;
    margin-top: 1.12em;
  }
  .footer-payment__item{
    height: 1.8em;
    flex-grow: 1;
    img{
      flex-grow: 1;
    }
  }
}


@media screen and (max-width: 992px){
  .blog-reviews{
    margin-top: 1.875em;
    @media screen and (min-width: 577px){
      .text21{
        text-align: center;
      }
      &__items{
        grid-template-columns: repeat(2,calc(50% - .625em));
        gap: 1.25em;
      }
    }
  }

  .pay-info,
  .delivery-info {
    @include lastM(0 0 1.875em);
  }
}


@media screen and (max-width: 576px){
  .blog-reviews{
    margin-top: 2.5em;
    &__items{
      gap: .94em;
    }
  }
}
