.grid-column3,
.grid-column4{
  display: grid;
  gap: 1.875em;
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 1.875em * 3 / 4));
}



@media screen and (max-width: 992px){
  .grid-column2-tablet,
  .grid-column3-tablet,
  .grid-column4-tablet{
    gap: 1.25em;
  }

  .grid-column2-tablet{
    grid-template-columns: repeat(2,calc(50% - .625em));
  }

  .grid-column3-tablet{
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
  }

  .grid-column4-tablet{
    grid-template-columns: repeat(4,calc(25% - 1.25em * 3 / 4));
  }
}

@media screen and (max-width: 576px){
  .grid-column2-mob{
    grid-template-columns: repeat(2,calc(50% - .47em));
    gap: .94em;
  }
}
