.contacts{
  padding: 2.19em 0 1.875em;
  &__top{
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    padding: 2.5em 3.125em;
    background: #FFF;
    box-shadow: 0px 26px 48px 0px rgba(0, 0, 0, 0.15), 0px 6px 7px 0px rgba(0, 0, 0, 0.05);
  }
  .map{
    height: 35.5em;
    margin-top: -5.0625em;
  }
}

.contact-item{
  display: flex;
  @media screen and (min-width: 993px){
    @include lastM(0 2em 0 0);
    br{
      display: none;
    }
  }
  &__icon{
    width: 1.9375em;
    flex-shrink: 0;
    margin-right: .56em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding: .19em 0;
  }
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 162%;
  }
  &__title{
    font-weight: 600;
    margin-bottom: .19em;
  }
  &__email{
    text-decoration: underline;
    color: $primary;
  }
}

.consult{
  padding: 1.875em 0 3.125em;
  position: relative;
  &__desc{
    margin-top: .5em;
    text-align: center;
  }
  &__petal{
    position: absolute;
    top: 5.625em;
    left: 7.625em;
    width: 4.5em;
  }
  &__petal2{
    position: absolute;
    left: 2.31em;
    top: 13.8em;
    width: 6.625em;
  }
  &__leaf{
    pointer-events: none;
    position: absolute;
    right: 0;
    top: 0;
    width: 31.125em;
  }
  .form-success{
    width: 42.8125em;
    max-width: 100%;
    margin: 2.25em auto 0;
    min-height: 32.625em;
  }
}

.consult-form{
  width: 42.8125em;
  max-width: 100%;
  margin: 2.25em auto 0;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .72em));
  gap: 1.44em;
  .m-check,
  ._full{
    grid-column: 1/-1;
  }
  .m-check{
    label{
      color: #78736F;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.error-message{
  padding: .625em .94em;
  border-radius: 0.1875em;
  border: 1px solid #ED1C24;
  background: #FFF8F8;
  color: #ED1C24;
  &__text{
    font-size: .8125em;
    line-height: 162%;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding-top: 1.25em;
    &__top{
      padding: 1.25em 2.81em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.06em 1.25em;
    }
    .map{
      margin-top: -6.5em;
    }
  }

  .consult{
    padding: 1.875em 0 2.5em;
    &__petal,
    &__petal2,
    &__leaf{
      display: none;
    }
    &__desc{
      margin-top: .625em;
    }
    .form-success{
      min-height: 29.25em;
      width: 100%;
      margin-top: 2.5em;
    }
  }

  .consult-form{
    margin-top: 2.5em;
    width: 100%;
    row-gap: .94em;
  }
}


@media screen and (max-width: 576px){
  .contacts{
    padding-bottom: 1.25em;
    &__top{
      padding: 1.25em .94em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    .map{
      margin-top: -8.5em;
    }
  }

  .consult{
    padding-top: 1.25em;
    .form-success{
      min-height: 39.25em;
      margin-top: 1.25em;
    }
  }

  .consult-form{
    margin-top: 1.25em;
    grid-template-columns: 100%;
  }
}
