.auth{
  padding: 2.19em 0 4em;
  &__inner{
    width: 87.5em;
    max-width: 100%;
    margin: 0 auto;
    border: 1px solid #EAEAEA;
    padding: 2.5em;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.auth-hint{
  max-width: 42.6875em;
  flex-grow: 1;
  padding-left: 3.125em;
  border-left: 1px solid #EAEAEA;
  display: flex;
  &__icon{
    flex-shrink: 0;
    margin-right: 1.81em;
    width: 2.375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    min-height: 2.8125em;
  }
  &__title{
    font-size: 1.75em;
    font-weight: 500;
  }
  &__text{
    color: #515151;
    margin-top: 2.25em;
    p{
      @include lastM(0 0 1em);
    }
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
  }
  &__link{
    padding: 0 3.75em;
  }
}

.reg{
  .auth__inner{
    width: 100%;
    margin-top: 1.25em;
    @media screen and (min-width: 993px){
      align-items: flex-start;
    }
  }
}

.pass-recover{
  &__inner{
    max-width: 100%;
    width: 29em;
    margin: 0 auto;
  }
  &__desc{
    text-align: center;
    color: #515151;
    .text16{
      line-height: 175%;
    }
  }
  &__img{
    width: 1.875em;
    display: block;
    margin: 0 auto 1.56em;
  }
  .m-btn{
    padding: 0;
  }
}

.pass-form{
  @include grid100gap(.94em);
  margin-top: 1.56em;
}

.error-output{
  @include grid100gap(.94em);
  color: #F81D2A;
  .text15{
    line-height: 120%;
  }
}


@media screen and (max-width: 992px){
  .auth{
    padding: 1.56em 0 2.5em;
    &__inner{
      align-items: flex-start;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 2.5em 1.25em;
      padding: 1.25em;
    }
  }

  .login-form{
    width: 100%;
  }

  .auth-hint{
    padding-left: 1.25em;
    margin: 0;
    max-width: 100%;
    display: block;
    text-align: center;
    &__icon{
      margin: 0 auto .31em;
      display: block;
    }
    &__top{
      min-height: unset;
      justify-content: center;
    }
    &__title{
      font-size: 1.375em;
      line-height: 130%;
    }
    &__text{
      margin-top: .69em;
      p{
        line-height: 157%;
      }
    }
    &__link{
      width: 100%;
    }
  }

  .pass-recover{
    &__inner{
      width: 25em;
    }
    &__desc{
      .text16{
        line-height: 157%;
      }
    }
  }

  .error-output{
    gap: .81em;
    .text15{
      font-size: .875em;
    }
  }
}



@media screen and (max-width: 576px){
  .auth{
    padding-top: 1.25em;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .auth-hint{
    padding-left: 0;
    border: none;
  }
}
