@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_grid.scss";
@import "components/_datepicker.scss";
.swiper-button{
  width: 4.4375em;
  height: 4.4375em;
  border: 1px solid #E9E1E8;
  border-radius: 50%;
  transition: all .3s;
  @include center;
  &-next{
    right: -7.5em;
  }
  &-prev{
    left: -7.5em;
  }
  svg{
    width: .5em;
    height: auto;
  }
  &:hover{
    box-shadow: 0 .9375em 1em 0 rgba(215, 165, 166, 0.14);
    background-color: #fff;
    border-color: #fff;
  }
}

._scale-hover{
  &:not(._not-hidden){
    overflow: hidden;
  }
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .3s;
  }
}

._backdrop{
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  background: $black;
  opacity: .9;
}
