.order-success{
  padding-bottom: 6.25em;
  &__inner{
    width: 72em;
    max-width: 100%;
    margin: 5.25em auto 0;
    @include grid100gap(2.31em);
  }
  &__thank{
    @include center;
    text-align: center;
    padding: .94em 3em;
    background: #F8F8F8;
    color: #414042;
    font-weight: 500;
  }
  .order-detail{
    margin: 0;
  }
  &__title{
    font-size: 1.75em;
    line-height: 115%;
    font-weight: 500;
    color: #1D1D27;
  }
}

.order-info{
  border: 1px solid #EAEAEA;
  padding: 2.5em;
  display: flex;
  justify-content: space-between;
  &__item{
    display: flex;
    align-items: center;
    &:not(:last-child){
      @media screen and (min-width: 993px){
        flex-grow: 1;
        &:after{
          content: '';
          height: 4.625em;
          width: 1px;
          border-right: 1px dashed #D7D7D7;
          min-height: 100%;
          flex-shrink: 0;
          margin: 0 auto;
        }
      }
    }
    &-inner{
      @include grid100gap(.44em);
    }
  }
  &__title{
    font-size: .875em;
    line-height: 129%;
    color: #4C4C4C;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 144%;
    color: #414042;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .order-success{
    padding-bottom: 5em;
    &__inner{
      margin-top: 1.25em;
      gap: 2em;
    }
    &__title{
      display: none;
    }
  }

  .order-info{
    padding: .94em;
  }
}


@media screen and (max-width: 576px){
  .order-info{
    @include grid100gap(.94em);
  }
}
