.modal,.search-modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba($black, 0.9);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &._active {
        opacity: 1;
        pointer-events: unset;
        .search-modal__bg,
        .modal__bg {
            display: block;
        }
    }
}

.modal {
    overflow-y: auto;
    padding: 1em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }


    &__dialog {
        background: #fff;
        width: 41.9375em;
        max-width: calc(100% - 1.875em);
        position: relative;
        z-index: 3;
        padding: 2.19em;
        display: inline-grid;
        grid-template-columns: 100%;
        gap: 1.25em;
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: 2em;
        display: flex;
        align-items: center;
        position: fixed;
        top: 3.125em;
        right: 3.125em;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                opacity: 1;
            }
        }
    }
    &__top{
        color: #17212C;
        text-align: center;
        @include grid100gap(1.25em);
    }
    &__text{
        letter-spacing: 0.02em;
        line-height: 206%;
        color: #534E54;
    }
}

.modal-form{
    @include grid100gap(1.56em);
    .m-check{
        label{
            color: #78736F;
        }
    }
    &__nav{
        display: flex;
        justify-content: center;
    }
}

.cookie-modal{
    position: fixed;
    bottom: 0;
    left: 0;
    background: #FFF;
    box-shadow: 0px -1px 55px 0px rgba(0, 0, 0, 0.08);
    z-index: 20;
    padding: 2.5em 0;
    width: 100%;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &._active {
        opacity: 1;
        pointer-events: unset;
    }
    &__inner{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 2.44em;
    }
    &__content{
        @include grid100gap(.5em);
        max-width: 61.5em;
    }
    &__title{
        line-height: 132%;
        font-weight: 500;
        color: #000;
    }
    &__desc{
        font-size: .9375em;
        line-height: 167%;
        color: #383838;
        a{
            text-decoration: underline;
        }
    }
    &__nav{
        display: flex;
    }
    &__btn{
        max-width: 100%;
        padding: 0 2.88em;
    }
}

.search-field{
    input{
        border: none;
        border-bottom: 1px solid #F3EDED;
        height: 4.8125em;
    }
    &__find{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 1.625em;
        @include center;
        svg{
            width: 100%;
            height: auto;
        }
    }
}

.search-modal{
    padding-top: 5.5em;
    overflow: hidden;
    &._active{
        .search-modal__dialog{
            transform: translateY(0);
        }
    }
    &__dialog{
        transform: translateY(130%);
        transition: all .4s;
        height: 100%;
        overflow-y: auto;
        width: 100%;
        position: relative;
        z-index: 2;
        background: #FFF;
        box-shadow: 0px -7px 47px 0px rgba(0, 0, 0, 0.13);
    }
    .search-field{
        input{
            padding: 0 8.75em;
            @media screen and (min-width: 1681px){
                padding: 0 calc(((100vw - 1680px - var(--scrollWidth)) / 2) + 8.75em);
            }
        }
        .fake-placeholder{
            left: 8.75em;
            @media screen and (min-width: 1681px){
                left: calc(((100vw - 1680px - var(--scrollWidth)) / 2) + 8.75em);
            }
        }
        &__find{
            right: 8.75em;
            @media screen and (min-width: 1681px){
                right: calc(((100vw - 1680px - var(--scrollWidth)) / 2) + 8.75em);
            }
        }
    }
    &__link{
        width: 100%;
        border-bottom: 1px solid #F3EDED;
        background: #FFFBFB;
        color: $primary;
        &:hover{
            background: darken(#FFFBFB,2%);
            color: $primary;
        }
    }
    &__close{
        display: none;
    }
}

.search-card{
    padding: 1.56em 0;
    border-bottom: 1px solid #F3EDED;
    &__inner{
        display: grid;
        grid-template-columns: 4.8125em calc(100% - 17.1875em) 10.125em;
        gap: 1.125em;
        align-items: center;
    }
    &__content{
        display: grid;
        grid-template-columns: calc(100% - 21.125em) 20em;
        gap: .375em 1.125em;
        align-items: center;
    }
    &__img{
        display: block;
        img{
            width: 100%;
            display: block;
        }
    }
    &__nums{
        display: flex;
        align-items: center;
    }
}



@media screen and (max-width: 992px){
    .modal{
        padding: 3em 0;
        &__dialog{
            gap: 1.25em;
            padding: 1.56em;
            width: 31.6875em;
        }
        &__close{
            position: absolute;
            top: unset;
            bottom: 100%;
            margin-bottom: .94em;
            right: 0;
            width: .875em;
        }
        &__top{
            gap: .75em;
        }
    }

    .modal-form{
        gap: 1.25em;
        &__nav{
            .m-btn{
                width: 100%;
            }
        }
        .m-check{
            label span{
                font-size: .8125em;
            }
        }
    }

    .thank-modal{
        .modal__dialog{
            gap: .94em;
        }
    }

    .search-modal{
        &__close{
            z-index: 10;
            @include center;
            position: absolute;
            top: 3.88em;
            right: 1.56em;
            width: .875em;
            svg{
                width: 100%;
                height: auto;
            }
        }
        .search-field{
            input{
                padding: 0 .94em;
            }
            .fake-placeholder{
                left: .94em;
            }
            &__find{
                right: .94em;
            }
        }
        &__link{
            height: 4.875em;
            span{
                font-size: .9375em;
            }
        }
    }

    .search-card{
        padding: 1.25em 0;
        &__inner{
            grid-template-columns: 3.6875em calc(100% - 14.88em) 9.3125em;
            gap: .94em;
        }
        &__content{
            grid-template-columns: 100%;
        }
        &__action{
            .m-btn{
                height: 2.25em;
                padding: 0;
            }
        }
    }

    .cookie-modal{
        padding: .94em 0;
        &__inner{
            display: grid;
            grid-template-columns: calc(100% - 12.9375em) 12.4375em;
            gap: .5em;
            padding: 0;
        }
        &__nav{
            margin:0;
        }
        &__btn{
            width: 100%;
            padding: 0;
            height: 2.75em !important;
            span{
                font-size: .9375em;
            }
        }
        &__desc{
            font-size: .8125em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &:after{
            //display: none;
        }
        &__dialog{
            margin: 0 auto;
        }
    }

    .search-modal{
        padding-top: 4em;
        &__close{
            top: 2.25em;
            right: .8em;
        }
    }

    .search-card{
        &__inner{
            grid-template-columns: 3.6875em calc(100% - 4.6275em);
            gap: .81em .94em;
        }
        &__action{
            grid-column: 1/-1;
        }
    }

    .cookie-modal{
        &__inner{
            grid-template-columns: 100%;
        }
        &__nav{
            .m-btn{
                width: auto;
                padding: 0 1.75em;
            }
        }
    }
}
