.catalog{
  padding: 3.125em 0;
}

.category-card{
  display: flex;
  flex-direction: column;
  border: 1px solid #F0EFED;
  padding: 1.25em;
  transition: all .3s;
  &:hover{
    border-color: $black;
  }
  &__img{
    display: block;
    @include imgCover(100%);
  }
  &__title{
    text-transform: uppercase;
    min-height: 4em;
    @include center;
    text-align: center;
    margin-top: .625em;
    flex-grow: 1;
  }
  &__nav{
    padding-top: .625em;
    margin-top: auto;
  }
}

.filter{
  border-top: 1px solid #F0EFED;
  border-bottom: 1px solid #F0EFED;
  margin-top: 2.69em;
  &__inner{
    display: flex;
  }
  &__clear{
    padding: 0 1.56em;
    height: 4.125em;
    text-transform: uppercase;
    border-right: 1px solid #F0EFED;
    background: #FAFAFA;
    color: #3E3E3E;
    &:hover{
      color: #3E3E3E;
      background: darken(#FAFAFA,5%);
    }
    svg{
      width: .75em;
      height: auto;
      flex-shrink: 0;
      margin-right: 1em;
    }
    span{
      font-size: .75em;
    }
    @media screen and (min-width: 993px){
      span{
        transform: translateY(-.0625em);
      }
    }
  }
  &__bg,
  &__close,
  &__confirm{
    display: none;
  }
}

.filter-item{
  padding: 0 1.56em;
  height: 4.125em;
  display: flex;
  align-items: center;
  border-right: 1px solid #F0EFED;
  &:first-child{
    padding-left: 0;
  }
  &:last-child{
    padding-right: 0;
    border-right: none;
  }
  &._price-filter{
    flex-grow: 1;
  }
  &._size-filter{
    .filter-select{
      width: 6.625em;
    }
  }
  &._type-filter{
    .filter-select{
      width: 6.0625em;
    }
  }
  &__label{
    font-weight: 500;
    text-transform: uppercase;
    margin-right: 1em;
  }
}

.filter-select{
  width: 10.75em;
  position: relative;
  &._active{
    .filter-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__field{
    input{
      cursor: pointer;
      width: 100%;
      height: 3.25em;
      border: none;
      border-bottom: 1px solid #C6C6C6;
      font-size: .75em;
      padding: 0 1.58em 0 .67em;
      text-transform: uppercase;
      background-color: transparent;
      background-image: url(../img/select-arrow.svg);
      background-repeat: no-repeat;
      background-position: right .2em center;
      background-size: .67em auto;
      &::placeholder{
        color: $gray;
      }
    }
  }
  &__dropdown{
    box-shadow: .5em .5em 2em rgba(0, 0, 0, 0.13);
    position: absolute;
    top: 100%;
    left: 0;
    min-width: 100%;
    padding: .625em 0;
    background-color: #fff;
    z-index: 3;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    max-height: 15em;
    overflow-y: auto;
    @include scroll;
  }
  .m-check{
    white-space: nowrap;
    padding: 0 .44em;
    min-height: 2.3125em;
    display: flex;
    align-items: center;
    label{
      font-weight: 500;
      padding-left: 2.125em;
      text-transform: uppercase;
      display: flex;
      align-items: center;
      span{
        font-size: .75em;
        letter-spacing: 0.015em;
      }
      &:before{
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
}

.filter-output{
  padding: .31em 0 1.31em;
}

.selected-filter,
.price-output{
  &__remove{
    margin-left: .5em;
    width: .5em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.selected-filter{
  display: none;
  align-items: center;
  flex-wrap: wrap;
  margin: 1em 1.25em 0 0;
  &._active{
    display: inline-flex;
  }
  &__title,
  &__item{
    span{
      font-size: .875em;
      line-height: 110%;
    }
  }
  &__title{
    color: #000;
  }
  &__item{
    margin-left: .625em;
    display: inline-flex;
    align-items: center;
    color: #676767;
  }
}

.filter-slider{
  display: flex;
  align-items: center;
  flex-grow: 1;
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
  .range-slider{
    width: 100%;
    background-color: #DFE0E2;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    cursor: pointer;
    &:before{
      //content: '';
      position: absolute;
      height: 500%;
      width: 100%;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      background-color: #fff;
      border: .125em solid $primary;
      width: .625em;
      height: .625em;
      top: -.3em !important;
      right: -.3em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: 0 0 0 1em;
    display: flex;
    align-items: center;
    &-split{
      font-size: 1em;
      margin: 0 .75em;
    }
  }
  .fg{
    width: 5.875em;
    input{
      height: 2.75em;
    }
  }
  &__currency{
    position: absolute;
    top: 50%;
    right: .5em;
    transform: translateY(-50%);
  }
}

.price-output{
  display: flex;
  align-items: center;
  margin-left: .625em;
  &__text{
    font-size: .875em;
    line-height: 110%;
    color: #676767;
  }
}

.catalog-list{
  padding: .625em 0 4.375em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-bottom: 1.625em;
    .fg{
      white-space: nowrap;
      margin-left: 1em;
      flex-shrink: 0;
      width: 15.5625em;
    }
    .current{
      color: #C1C1C1;
    }
  }
}

.pagination{
  margin-top: 3.125em;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 3.5em;
    min-width: 3.5em;
    padding: 0 1.25em;
    border: 1px solid #DFDFDF;
    @include center;
    margin: 0 .3125em;
    color: $black;
    svg{
      width: 1.4375em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $black;
      border-color: $black;
      path{
        fill: #fff;
      }
    }
    &._btn{
      svg{
        &:first-child{
          margin-right: .625em;
        }
        &:last-child{
          margin-left: .625em;
        }
      }
    }
  }
}

.show-menu{
  display: none;
}


@media screen and (max-width: 992px){
  .catalog{
    padding: 1.44em 0 1.875em;
  }

  .category-card{
    &__top{
      padding: 0 .8125em;
    }
    &__title{
      margin-top: 1.31em;
    }
    .m-btn{
      height: 2.625em;
      padding: 0 1em;
    }
  }

  .pagination{
    margin-top: 3.125em;
    ._hide-tablet{
      display: none;
    }
    ._btn{
      padding: 0;
      span{
        display: none;
      }
      svg{
        margin: 0 !important;
      }
    }
  }

  .filter{
    position: fixed;
    margin: 0;
    top: 0;
    left: 0;
    width: 19.9375em;
    height: $vh;
    max-width: calc(100% - 2.5em);
    background-color: #fff;
    overflow-y: auto;
    z-index: 21;
    transition: all .3s;
    transform: translateX(-120%);
    &._open{
      transform: translateX(0%);
      &~.filter__bg,
      &~.filter__close{
        display: flex;
      }
    }
    .container{
      padding: 0;
    }
    &__inner{
      padding: 1.56em 0;
      display: block;
    }
    &__clear,
    &__confirm{
      margin: .94em auto 0;
      width: calc(100% - 3.12em);
      height: 3.625em;
    }
    &__clear{
      border: none;
    }
    &__confirm{
      padding: 0;
      display: flex;
      margin-top: .625em;
    }
    &__close{
      position: fixed;
      top: .875em;
      right: .875em;
      width: .875em;
      @include center;
      display: none;
      z-index: 21;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }

  .filter-item{
    padding: 1.56em !important;
    border-right: none !important;
    border-bottom: 1px solid #F0EFED;
    display: block;
    height: unset;
    &__label{
      margin: 0 0 .625em;
      font-weight: 600;
      text-transform: unset;
      span{
        font-size: 1.125em;
        line-height: 128%;
        letter-spacing: 0.0225em;
      }
    }
  }

  .filter-select{
    &__field{
      display: none;
    }
    &__dropdown{
      position: static;
      margin: 0;
      padding: 0;
      width: 100%;
      opacity: 1;
      pointer-events: unset;
      background-color: transparent;
      box-shadow: unset;
      max-height: unset;
      overflow: unset;
    }
  }

  .filter-slider{
    display: block;
    &__item{
      width: 100%;
    }
    &__inputs{
      margin: 1.5em 0 0;
      .fg{
        flex-grow: 1;
      }
    }
  }

  .show-menu{
    @include center;
    border-top: 1px solid #F0EFED;
    border-bottom: 1px solid #F0EFED;
    height: 3.5625em;
    font-weight: 600;
    text-transform: uppercase;
    color: #44413F;
    margin-top: 1.875em;
    svg{
      width: 1.25em;
      height: auto;
      margin-right: .625em;
    }
    span{
      font-size: .8125em;
      line-height: 192%;
    }
  }

  .selected-filter{
    &__title,
    &__item{
      span{
        font-size: .75em;
        line-height: 110%;
      }
    }
  }

  .catalog-list{
    padding: 0 0 1.875em;
    &__top{
      margin-bottom: 1.875em;
      .nice-select{
        height: 3.4375em;
        .option{
          height: 3.4375em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    padding-top: 1.875em;
  }

  .category-card{
    padding: .94em .625em;
    &__top{
      padding: 0;
    }
    &__title{
      margin-top: .37em;
    }
    &__nav{
      padding-top: .37em;
    }
  }

  .pagination{
    margin-top: 2.5em;
    &__item{
      margin: 0 .1875em;
      &._btn{
        padding: 0;
        span{
          display: none;
        }
        svg{
          margin: 0 !important;
        }
      }
    }
  }

  .catalog-list{
    padding: 0 0 1.875em;
    &__top{
      .fg{
        width: 100%;
        margin: 0;
      }
      .text16{
        display: none;
      }
    }
  }

  .selected-filter{
    margin-top: .5em;
    &__title{
      margin-right: .44em;
      padding: .2em 0;
    }
    &__item{
      margin: .25em .625em 0 0;
    }
  }
}
