@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 3.125em;
    height: 4.125em;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    &:disabled{
        opacity: .3;
        pointer-events: none;
    }
    span{
        font-size: 1em;
        line-height: 175%;
    }
    &._small{
        height: 3.25em;
    }
    &._medium{
        height: 3.875em;
    }
}

.m-btn-black{
    background-color: $black;
    color: #fff;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}

.m-btn-black-outline{
    color: $black;
    border: 1px solid $black;
    &:hover{
        background-color: $black;
        color: #fff;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    font-weight: 500;
    color: $primary;
    &:hover{
        span{
            text-decoration-line: underline;
        }
    }
    svg{
        width: .3125em;
        height: auto;
        margin-left: .375em;
    }
    span{
        font-size: 0.875em;
        line-height: 193%;
        transition: inherit;
    }
}

.m-btn-gray{
    background: #FBFBFB;
    color: #1E1E1E;
}
