.reviews-collection{
  padding: 3.125em 0;
}

.reviews-slider{
  margin-top: 3em;
  .swiper{
    &-slide{
      width: 50%;
    }
  }
}

.review-card{
  border: 1px solid #F3F3F1;
  padding: 1.875em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name,
  &__date{
    line-height: 187%;
    color: #41393F;
  }
  &__text{
    margin-top: .75em;
    color: #5B4E59;
    .text16{
      line-height: 188%;
    }
  }
  &__rating{
    margin-top: .75em;
    display: flex;
    align-items: center;
    .rating{
      margin-left: .56em;
    }
  }
}

.about{
  border-radius: 3.125em 3.125em 0 0;
  background: rgba(215, 183, 165, 0.14);
  margin: 6.875em 0 4.375em;
  padding: 4.38em 0 4.87em;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: 29.3125em 26.4375em;
    justify-content: space-between;
    margin-top: 4.75em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__img{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 42.75em;
    max-width: 100%;
    img{
      width: 100%;
      filter: drop-shadow(19px 14px 52px rgba(197, 148, 121, 0.14))
      drop-shadow(19px 14px 52px rgba(197, 148, 121, 0.14))
      drop-shadow(170px 125px 126px rgba(197, 148, 121, 0.07))
      drop-shadow(302px 222px 150px rgba(197, 148, 121, 0.02));
      display: block;
      position: relative;
      z-index: 2;
    }
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      height: calc(100% - 8em);
      width: calc(100% - 9em);
      border-radius: 19.78125em 19.78125em 0 0;
      opacity: 0.4;
      background: #FFF;
    }
    ._view2{
      display: none;
    }
  }
  &__leaf{
    position: absolute;
    top: -2.5em;
    right: 5.69em;
    width: 8.3125em;
  }
  &__leaf2{
    position: absolute;
    right: 7.39em;
    bottom: -3.36em;
    width: 5.75em;
  }
  &__petal{
    position: absolute;
    width: 4.5em;
    left: 8.56em;
    bottom: 14.06em;
  }
  &__petal2{
    position: absolute;
    left: 3.25em;
    bottom: 1.06em;
    width: 6.625em;
  }
}

.about-item{
  display: flex;
  align-items: flex-start;
  @include lastM(0 0 3.75em);
  &__icon{
    width: 4.6875em;
    height: 4.6875em;
    margin-right: 1.06em;
    flex-shrink: 0;
    border-radius: 50%;
    border: 1px solid #ECDED8;
    @include center;
    img{
      width: 2.625em;
    }
  }
  &__content{
    padding: .31em 0;
    @include grid100gap(.375em);
  }
  .text15{
    text-transform: uppercase;
    color: #55483E;
  }
}

.instagram{
  padding: 3.125em 0;
  &__desc{
    text-align: center;
    color: #77706B;
  }
}

.instagram-slider{
  margin-top: 2.44em;
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.instagram-card{
  display: block;
  &__img{
    @include imgCover(100%);
  }
}


@media screen and (max-width: 1850px){
  .about{
    &__petal{
      left: 3em;
    }
    &__petal2{
      left: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .reviews-collection{
    padding: 1.875em 0;
  }

  .reviews-slider{
    margin-top: 1.56em;
  }

  .review-card{
    padding: 1.56em;
  }

  .about{
    border-radius: 1.875em 1.875em 0 0;
    margin: 3.5em 0 2.5em;
    padding: 1.875em 0 0;
    .container{
      overflow: hidden;
    }
    &__inner{
      grid-template-columns: 20.625em calc(100% - 20.625em);
      gap: .19em 0;
      margin-top: 1.56em;
      position: relative;
    }
    &__text{
      grid-column: 1/-1;
    }
    &__img{
      max-width: unset;
      width: calc(100% + 1.5em + .9375em);
      margin-left: -1.5em;
      transform: unset;
      position: relative;
      left: unset;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      //overflow: hidden;
      img{
        height: 30.4375em;
        width: auto;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      &:before{
        width: 24em;
        height: calc(100% - 5.75em);
      }
    }
    &__nav{
      margin: 0;
      position: absolute;
      left: 0;
      bottom: 2.5em;
    }
    &__items{
      padding: 2.13em 0 7.87em;
    }
    &__leaf{
      width: 5.125em;
      right: 2.5em;
      top: -1.62em;
    }
    &__leaf2{
      width: 3em;
      right: 3.69em;
      bottom: -1.2em;
    }
    &__petal{
      top: 17.44em;
      left: unset;
      right: 20.13em;
      width: 2.4375em;
    }
    &__petal2{
      top: 21.94em;
      right: 21.87em;
      left: unset;
      width: 3.625em;
    }
  }

  .about-item{
    @include lastM(0 0 1.56em);
    &__icon{
      width: 4.125em;
      height: 4.125em;
      margin-right: .94em;
      img{
        width: 2.3625em;
      }
    }
  }

  .instagram{
    padding: 1.875em 0;
  }

  .instagram-slider{
    margin-top: 1.875em;
  }
}


@media screen and (max-width: 576px){
  .reviews-slider{
    .swiper-slide{
      width: 100%;
    }
  }

  .about{
    &__inner{
      margin-top: .94em;
      grid-template-columns: 100%;
    }
    &__items{
      padding-bottom: 5.38em;
    }
    &__nav{
      left: 50%;
      transform: translateX(-50%);
      bottom: 18.69em;
      .m-btn{
        white-space: nowrap;
      }
    }
    &__img{
      height: 18.69em;
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
      img{
        height: auto;
        width: 100%;
        top: 50%;
        left: 50%;
        position: absolute;
        transform: translate(-57%,-50%);
      }
    }
    &__leaf{
      right: .81em;
    }
    &__leaf2{
      right: .94em;
    }
    &__petal,
    &__petal2{
      top: unset;
      right: unset;
    }
    &__petal{
      left: 1.62em;
      bottom: 16em;
    }
    &__petal2{
      left: -1.31em;
      bottom: 8.94em;
    }
  }

  .instagram-slider{
    margin-top: 1.56em;
    .swiper-slide{
      width: calc(100% / 3);
    }
  }
}
