.cart-page{
  @media screen and (min-width: 993px){
    .cart__sidebar{
      padding-top: 3.375em;
    }
  }
}

.cart{
  padding: 2.19em 0 4.125em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 27.9375em - 1.875em) 27.9375em;
    gap: 3.75em 1.875em;
  }
}

.order-sum,
.cart-sum{
  position: sticky;
  top: 1em;
  border: .4375em solid #F0EFED;
  padding: 1.75em;
  @include grid100gap(1.69em);
}

.prop-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .375em;
    border-top: 1px dashed #D1D1D1;
    margin: 0 .94em;
  }
  &__title{
    color: #000;
    font-size: .9375em;
    line-height: 187%;
  }
  &__desc{
    order: 2;
    font-size: 1.125em;
    line-height: 111%;
    font-weight: 600;
  }
  &._type2{
    .prop-item__desc{
      font-size: 1.375em;
    }
  }
}

.promo-code{
  @include grid100gap(.94em);
  .fg{
    input{
      padding-right: 1.5em;
    }
  }
  &__btn{
    ._view3,
    ._view2{
      display: none;
    }
    svg{
      margin-right: .625em;
      width: 1.0625em;
      height: auto;
    }
  }
  &__error{
    display: none;
    text-align: center;
    color: #FF1515;
    font-size: 0.8125em;
    line-height: 115%;
  }
  &._success{
    .promo-code__btn{
      pointer-events: none;
      border-color: $primary;
      color: $primary;
    }
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
  &._error{
    input{
      &::placeholder{
        color: #FF1515;
      }
    }
    input,
    .promo-code__btn{
      border-color: #FF1515;
      color: #FF1515;
    }
    .promo-code__btn{
      pointer-events: none;
    }
    ._view1{
      display: none;
    }
    .promo-code__error,
    ._view3{
      display: block;
    }
  }
}

.cart-table{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 29.75em) 12.75em 9.5em 3.75em;
    gap: 1.25em;
  }
}

.cart-item,
.cart-item2{
  &__img{
    @include imgCover(138%);
  }
  .cartcalc{
    max-width: 6.9375em;
    button,
    input{
      height: 3.125em;
    }
  }
}

.cart-item{
  align-items: center;
  border-bottom: 1px solid #F0EFED;
  padding: 1.25em 0;
  &__content{
    display: grid;
    grid-template-columns: 5.5625em calc(100% - 5.5625em - 1.125em);
    gap: 1.125em;
    align-items: center;
  }
  &__nums{
    display: flex;
    align-items: center;
    margin-top: 1em;
  }
  &__action{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}

.delete-btn{
  width: 1.125em;
  svg{
    width: 100%;
    height: auto;
  }
  &:hover{
    path{
      stroke: #FF1515;
    }
  }
}

.table-head{
  padding: .94em 0;
  border-bottom: 1px solid #DFDFDF;
  &__col{
    font-size: .875em;
    line-height: 172%;
    text-transform: uppercase;
    font-weight: 700;
    color: #373737;
  }
}

.cart-empty{
  padding: 3.75em 0 4.4em;
  &__inner{
    @include grid100gap(1.25em);
    margin: 0 auto;
    max-width: 100%;
    width: 29.625em;
    text-align: center;
  }
  &__img{
    width: 4.85625em;
    margin: 0 auto;
  }
  .text14{
    color: #383838;
    a{
      font-weight: 700;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}


@media screen and (max-width: 992px){
  .cart{
    padding: 2.5em 0 1.875em;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .cart-sum{
    padding: 1.125em;
    gap: 1.38em;
  }

  .promo-code{
    @media screen and (min-width: 577px){
      margin-bottom: -.44em;
      grid-template-columns: 50% 50%;
      column-gap: 0;
      align-items: flex-end;
    }
    &__error{
      order: 2;
      grid-column: 1/-1;
    }
    &__btn{
      height: 3.9375em !important;
    }
  }

  .cart-table{
    .table-head{
      display: none;
    }
  }

  .cart-item{
    grid-template-columns: calc(100% - 9.125em - 1.25em) 9.125em;
    row-gap: .79em;
    padding-left: 6.5em;
    position: relative;
    min-height: 10.1875em;
    &__img{
      position: absolute;
      left: 0;
      top: 1.25em;
      width: 5.5625em;
      height: 7.6875em;
      padding: 0;
    }
    &__content{
      grid-template-columns: 100%;
    }
    &__action{
      @media screen and (min-width: 577px){
        grid-column: 2/3;
        grid-row: 1/2;
        align-self: flex-start;
      }
    }
    &__nums{
      margin-top: .69em;
    }
    .text21{
      line-height: 111%;
    }
  }

  .cart-empty{
    padding: 3.125em 0 1.875em;
    &__inner{
      width: 20.625em;
    }
  }
}


@media screen and (max-width: 576px) {
  .cart {
    padding-top: 1.25em;
    &__inner {
      gap: 1.875em;
    }
  }

  .cart-item{
    grid-template-columns: calc(100% - 2.75em) 1.5em;
    &__sum,
    &__content{
      grid-column: 1/-1;
    }
    &__sum{
      order: 2;
    }
  }

  .cart-sum{
    &__btn{
      padding: 0;
      span{
        font-size: .9375em;
      }
    }
  }
}
