.slider{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
}

.banners-slider{
  overflow: hidden;
  position: relative;
  .swiper{
    &-button{
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      &-next{
        right: 1.25em;
      }
      &-prev{
        left: 1.25em;
      }
    }
  }
}

.banner{
  border-radius: 0 0 3.125em 3.125em;
  background: #FAF5FB;
  padding: 9.81em 0;
  position: relative;
  overflow: hidden;
  .container{
    position: relative;
    z-index: 2;
  }
  &._type2{
    padding: 7.3em 0;
    background: #FFFCE1;
    @media screen and (min-width: 993px){
      .banner__text{
        text-align: left;
        margin: 0;
      }
      .banner__desc{
        margin-left: 0;
      }
      .banner__nav{
        justify-content: unset;
      }
    }
  }
  &__img{
    pointer-events: none;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      height: 100%;
      width: 100%;
      display: block;
      object-fit: cover;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__text{
    text-align: center;
    width: 43.5em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__desc{
    margin: .56em auto 0;
    color: #554F4F;
    width: 27.625em;
    max-width: 100%;
  }
  &__nav{
    margin-top: 1.625em;
    display: flex;
    justify-content: center;
  }
  &__flower{
    position: absolute;
    right: 6.25em;
    bottom: 0;
    width: 49.5em;
  }
}

.advantages{
  padding: 3.125em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.advantage-item{
  display: flex;
  align-items: center;
  @media screen and (min-width: 993px){
    @include lastM(0 2em 0 0);
  }
  &__icon{
    width: 2.9375em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
    }
  }
  .text15{
    text-transform: uppercase;
  }
  .text14{
    color: #373232;
  }
}

.collection{
  padding: 3.125em 0;
}

.line-title{
  text-align: center;
  @include center;
  &:before,
  &:after{
    content: '';
    width: 2.75em;
    height: 1px;
    background: #DACFD0;
    flex-shrink: 0;
    margin: 0 .94em;
    transform: translateY(-.375em);
  }
  .text43{
    display: flex;
    align-items: center;
  }
}


.blog-slider,
.instagram-slider,
.reviews-slider,
.product-slider{
  margin-top: 3.125em;
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      padding-right: 1.875em;
    }
  }
}

.product-slider{
  .swiper{
    &-slide{
      width: 25%;
    }
    @media screen and (min-width: 993px){
      &-button{
        top: 11.62em;
        margin: 0;
      }
    }
  }
}

.product-card{
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-bottom: 1.25em;
  &__img{
    display: block;
    @include imgCover(136.2%);
  }
  .favourite-btn{
    position: absolute;
    top: 1.25em;
    right: 1.25em;
  }
  &__content{
    margin-top: 1.25em;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__reviews{
    margin-top: .5em;
    display: flex;
    align-items: center;
    .rating{
      margin-right: .56em;
    }
    .text12{
      color: #656670;
    }
  }
  &__nums{
    display: flex;
    align-items: center;
    margin-top: .56em;
  }
  &__price{
    margin-right: .5em;
  }
  &__price2{
    //text-decoration: line-through;
    color: #878890;
    margin-right: .5em;
    span{
      position: relative;
      display: inline-block;
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        width: 100%;
        height: .0625em;
        transform: translateY(-50%);
        background-color: #878890;
      }
    }
  }
  &__nav{
    width: 100%;
    padding-top: 1.25em;
    margin-top: auto;
    .m-btn{
      width: 100%;
    }
  }
}

.favourite-btn{
  &__icon{
    width: 2.375em;
    height: 2.375em;
    @include center;
    background-color: $primary;
    border-radius: 50%;
    transition: all .3s;
    svg{
      width: 1.375em;
      height: auto;
    }
  }
  &._active{
    .favourite-btn__icon{
      path{
        fill: #fff;
      }
    }
  }
  &._active,
  &:hover{
    .favourite-btn__icon{
      background-color: $red;
    }
  }
}

.m-badge{
  padding: 0 .3125em;
  border: 1px solid $primary;
  height: 1.4375em;
  display: flex;
  align-items: center;
  color: $primary;
  font-weight: 800;
  span{
    font-size: 0.6875em;
    line-height: 172%;
    letter-spacing: 0.01375em;
  }
}

.cart-btn{
  ._view2,
  ._mob{
    display: none;
  }
  &._active{
    background-color: $black;
    color: #fff;
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
}

.more{
  display: flex;
  justify-content: center;
  margin-top: .625em;
  &__btn{
    height: 4.625em;
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .44em;
    }
    span{
      font-size: .9375em;
    }
    &:hover{
      background-color: $black;
      color: #fff;
      path{
        stroke: #fff;
      }
    }
  }
}

.bunch-day{
  padding: 4.38em 0 0;
  margin: 4.375em 0;
  border-radius: 3.125em 3.125em 0 0;
  background: #F8FAEF;
  &__inner{
    margin-top: 1.12em;
    display: grid;
    grid-template-columns: 23.5625em calc(100% - 47.125em) 23.5625em;
  }
  &__info{
    padding: 7.87em 0;
    .text{
      margin-top: 1em;
    }
  }
  &__img{
    align-self: flex-end;
    width: 40em;
    max-width: 100%;
    margin: 0 auto;
    position: relative;
    img{
      width: 100%;
      display: block;
      filter: drop-shadow(9px 14px 37px rgba(0, 0, 0, 0.05))
              drop-shadow(36px 56px 67px rgba(0, 0, 0, 0.04))
              drop-shadow(81px 127px 90px rgba(0, 0, 0, 0.03))
              drop-shadow(144px 226px 107px rgba(0, 0, 0, 0.01))
              drop-shadow(224px 353px 117px rgba(0, 0, 0, 0.00));
    }
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      height: calc(100% - 1.5em);
      width: calc(100% - 6.2em);
      border-radius: 19.78125em 19.78125em 0 0;
      opacity: 0.6;
      background: #FFF;
    }
  }
}

.bunch-offer{
  width: 19.375em;
  max-width: 100%;
  margin-left: auto;
  padding: 9.31em 0;
  .text18{
    line-height: 111%;
  }
  &__prices{
    margin-top: 1.31em;
    &-top{
      display: flex;
      align-items: center;
      margin-bottom: .5em;
    }
    .m-badge{
      margin-left: .81em;
      height: 1.6875em;
      padding: 0 .44em;
    }
  }
  &__price2{
    text-decoration: line-through;
    color: #878890;
  }
  &__nav{
    margin-top: 2.31em;
    @include grid100gap(.94em);
  }
  &__title{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .banner{
    min-height: 29.4375em;
    padding: 5.38em 0;
    border-radius: 0 0 1.875em 1.875em;
    &._type2{
      padding: 2.69em 0;
      .banner__text{
        width: 28.9375em;
      }
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__text{
      width: 26.7em;
    }
    &__nav{
      margin-top: .625em;
    }
  }

  .advantages{
    padding: 1.875em 0;
    &__inner{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .56em));
      gap: 1.94em 1.12em;
    }
  }

  .collection{
    padding: 1.875em 0;
    .more{
      display: none;
    }
  }

  .line-title{
    &:before,
    &:after{
      width: 1.5625em;
      margin: 0 .625em;
      transform: translateY(-.25em);
    }
    ._hide-tablet{
      display: none;
    }
  }

  .blog-slider,
  .instagram-slider,
  .reviews-slider,
  .product-slider{
    margin-top: 1.25em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
      }
      &-nav{
        @include center;
        margin-top: 1.25em;
      }
      &-button{
        transform: unset;
        margin: 0 .47em;
        position: static;
      }
    }
  }

  .product-card{
    padding-bottom: 0;
    .favourite-btn{
      right: .625em;
      top: .625em;
    }
    &__content{
      margin-top: .75em;
    }
    &__reviews{
      .rating{
        margin-right: .31em;
      }
    }
    &__nums {
      margin-top: .69em;
    }
    .product-card__price2{
      order: 2;
      width: 100%;
    }
    .product-card__nums{
      flex-wrap: wrap;
    }

    &__nav{
      padding-top: 1.06em;
    }
    .cart-btn{
      padding: 0 1em;
      height: 2.375em;
      span{
        font-size: .875em;
      }
    }
  }

  .favourite-btn{
    &__icon{
      width: 1.75em;
      height: 1.75em;
      svg{
        width: 1em;
      }
    }
  }

  .m-badge{
    padding: 0 .25em;
    span{
      font-size: .625em;
    }
  }

  .cart-btn{
    ._desktop{
      display: none;
    }
    ._mob{
      display: block;
    }
  }

  .bunch-day{
    border-radius: 1.875em 1.875em 0 0;
    padding: 2.5em 0 0;
    margin: 1.875em 0;
    &__inner{
      margin-top: 1.44em;
      grid-template-columns: calc(100% - 18.8125em - 1.13em) 18.8125em;
      justify-content: space-between;
    }
    &__info{
      display: none;
    }
    &__img{
      display: block;
      &:before{
        height: calc(100% + .5em);
        width: calc(100% - 1.5em);
      }
    }
  }

  .bunch-offer{
    padding: 2.06em 0;
    text-align: center;
    &__title{
      color: #313329;
      display: block;
    }
    &__subtitle{
      margin-top: .94em;
    }
    &__prices{
      margin-top: .94em;
      @include center;
      &-top{
        margin: 0 .625em 0 0;
      }
      .m-badge{
        height: 1.5625em;
        padding: 0 .31em;
        margin-left: .625em;
      }
    }
    &__nav{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .banners-slider{
    .swiper{
      &-nav{
        position: absolute;
        bottom: 1.56em;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        align-items: center;
        z-index: 2;
      }
      &-button{
        transform: unset;
        margin: 0 .47em;
        position: static;
      }
    }
  }

  .banner{
    padding: 2.875em 0;
    min-height: 39.3125em;
    &._type2{
      padding: 2.875em 0;
    }
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }

  .advantages{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .instagram-slider,
  .reviews-slider,
  .product-slider{
    .swiper{
      &-container{
        width: calc(100% + .94em);
      }
      &-slide{
        padding-right: .94em;
      }
      &-nav{
        margin-top: 1.875em;
      }
    }
  }

  .product-slider{
    .swiper-slide{
      width: 50%;
    }
  }

  .bunch-day{
    &__inner{
      margin-top: .94em;
      grid-template-columns: 100%;
    }
    &__img{
      &:before{
        height: calc(100% - .75em);
        width: calc(100% - 3.25em);
      }
    }
  }

  .bunch-offer{
    padding: 0;
    width: 100%;
    order: -1;
  }
}
