.text{
  color: #5B4E59;
  li,
  p{
    font-size: 1em;
    line-height: 187.5%;
  }
  p{
    @include lastM(0 0 .875em);
  }
  b{
    font-weight: 700;
  }
  &.lh206{
    li{
      line-height: 220%;
    }
    p{
      line-height: 206%;
      @include lastM(0 0 1.125em);
    }
  }
  ul{
    padding-left: .94em;
    @include grid100gap(.375em);
    li{
      position: relative;
      padding-left: 1.0625em;
      &:before{
        content: '';
        position: absolute;
        top: .84em;
        left: 0;
        width: 0.375em;
        height: 0.375em;
        border-radius: 50%;
        background-color: $primary;
      }
    }
  }
  img{
    display: block;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
    margin-bottom: 1.125em;
    &._small{
      width: 26.5625em;
    }
  }
  hr{
    height: .875em;
    display: block;
    opacity: 0;
  }
  h3{
    font-size: 2.0625em;
    line-height: 128%;
    font-weight: 600;
    margin-bottom: .5em;
  }
  h4{
    font-size: 1.125em;
    line-height: 128%;
    font-weight: 600;
    margin-bottom: .5em;
  }
  blockquote{
    margin-bottom: 1.125em;
    font-weight: 500;
    padding: 1.125em 1.56em;
    border: .4375em solid #FBF7F7;
    p{
      font-size: 1.125em;
      line-height: 158%;
    }
  }
}


@media screen and (max-width: 992px){
  .text{
    p{
      font-size: .875em;
      line-height: 172%;
    }
    &.lh206{
      li{
        line-height: 167%;
      }
      p{
        line-height: 192%;
      }
    }
    ul{
      li{
        font-size: .9375em;
        &:before{
          top: .69em;
        }
      }
    }
    blockquote{
      p{
        font-size: 1em;
        line-height: 162%;
      }
    }
    h3{
      font-size: 1.6875em;
    }
  }
}
