@import "../vars.scss";
.breadcrumb {
    padding: .94em 0;
    text-align: center;
    &:not(:last-child){
        margin-bottom: .94em;
    }
    &__item {
        position: relative;
        font-weight: 500;
        display: inline;
        color: #44413F;
        text-transform: uppercase;
        &:last-child {
            opacity: 0.8;
        }
        &:not(:first-child) {
            margin-left: 2.12em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 0.25em;
                height: 0.25em;
                background-color: $pinkLight;
                border-radius: 50%;
                top: .625em;
                right: 100%;
                margin-right: .94em;
            }
        }
        span{
            font-size: .75em;
            line-height: 275%;
            letter-spacing: 0.0525em;
        }
    }
}

.page-top{
    &__desc{
        text-align: center;
        margin-top: .625em;
        font-weight: 500;
        color: #B1A3A3;
        text-transform: uppercase;
    }
}


@media screen and (max-width: 992px){
    .breadcrumb{
        padding: .625em 0;
        &:not(:last-child){
            margin-bottom: 1.875em;
        }
        &._mb10:not(:last-child){
            margin-bottom: .625em;
        }
        &__item{
            span{
                font-size: .6875em;
                line-height: 227%;
                letter-spacing: 0.04813em;
            }
        }
    }
}

@media screen and (max-width: 576px){
    .breadcrumb{
        &._mb10:not(:last-child){
            margin-bottom: 1.25em;
        }
    }
}
