.reviews-page{
  padding: 2.19em 0 6em;
  &__items{
    @include grid100gap(1.875em);
  }
}

.feedback{
  border-radius: 3.125em;
  background: rgba(215, 183, 165, 0.14);
  padding: 4.4em 0;
  position: relative;
  .text43{
    text-align: center;
  }
  &__petal{
    position: absolute;
    top: 26.5em;
    left: 8.56em;
    width: 4.5em;
  }
  &__petal2{
    position: absolute;
    left: 3.25em;
    bottom: 16.7em;
    width: 6.625em;
  }
  &__petal3{
    position: absolute;
    right: 14em;
    bottom: 16.86em;
    width: 10.125em;
  }
  .form-success{
    max-width: 49.5em;
    margin: 2.5em auto 0;
  }
}

.feedback-form{
  max-width: 49.5em;
  margin: 2.5em auto 0;
  ._full{
    grid-column: 1/-1;
  }
  .rating-input{
    &__item{
      width: 1.0625em;
      path{
        fill: #D1CECC;
      }
      &:hover {
        path{
          fill: $black;
        }
        &~.rating-input__item {
          path{
            fill: $black;
          }
        }
      }
    }
  }
  .rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
  .rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
  .rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
  .rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
  .rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
    path{
      fill: $black;
    }
  }
}


@media screen and (max-width: 992px){
  .reviews-page{
    padding: 1.25em 0 5.44em;
    &__items{
      gap: 1.25em;
    }
  }

  .feedback{
    border-radius: 1.875em;
    padding: 1.875em 0 2.5em;
    &__petal{
      left: 0;
      top: 9.81em;
      width: 3.31em;
    }
    &__petal2{
      width: 4.4375em;
      left: -.625em;
      bottom: 8.25em;
    }
    &__petal3{
      right: -2.48em;
      bottom: 11.69em;
      width: 6em;
    }
    &__wrap{
      overflow: hidden;
      position: relative;
    }
    .form-success{
      margin-top: .94em;
    }
  }

  .feedback-form{
    max-width: 38.25em;
    margin-top: .94em;
  }
}


@media screen and (max-width: 576px){
  .reviews-page{
    padding-bottom: 3.75em;
  }

  .feedback{
    padding-bottom: 3.63em;
    &__petal{
      width: 3.3125em;
      top: 5.62em;
      left: -.94em;
    }
    &__petal2,
    &__petal3{
      display: none;
    }
  }
}
