.error404{
  padding: 7em 0 4em;
  position: relative;
  &__inner{
    width: 42.5em;
    max-width: 100%;
    margin: 0 auto;
    @include grid100gap(2.19em);
  }
  &__img{
    margin: 0 auto;
    display: block;
    width: 100%;
    max-width: 35em;
  }
  &__text{
    margin: 0 auto;
    max-width: 34em;
    color: #292929;
    text-align: center;
    .text18{
      line-height: 161%;
    }
  }
  .search-field{
    input{
      border: 1px solid #DFDFDF;
      height: 4.125em;
    }
    &__find{
      right: 1.25em;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
  &__leaf{
    width: 30.375em;
    position: absolute;
    right: 0;
    top: -14em;
    pointer-events: none;
  }
  &__petal{
    bottom: 19em;
    left: 22.06em;
    position: absolute;
    width: 4.5em;
  }
  &__petal2{
    bottom: 10em;
    left: 5.81em;
    position: absolute;
    width: 6.625em;
  }
  &__leaf2{
    position: absolute;
    top: 13.24em;
    left: 50%;
    transform: translateX(-20em);
  }
  &__leaf3{
    position: absolute;
    width: 8.3125em;
    top: 14em;
    left: 50%;
    transform: translateX(13em);
    z-index: -1;
  }
}


@media screen and (max-width: 992px){
  .error404{
    padding: 3.44em 0 2.5em;
    &__inner{
      width: 30.375em;
      gap: 1.56em;
    }
    &__leaf{
      width: 15.625em;
      top: 11em;
    }
    &__leaf2{
      display: none;
    }
    &__leaf3{
      width: 6.5em;
      right: 7.13em;
      top: 9.6em;
      left: unset;
      transform: unset;
    }
    &__petal{
      width: 4em;
      left: 4.31em;
      top: 5.31em;
    }
    &__petal2{
      left: -1.69em;
      width: 5.375em;
      bottom: 13em;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    padding-top: 2.69em;
    &__petal{
      top: 8.25em;
      left: 8em;
      z-index: -1;
      width: 2.5625em;
    }
    &__petal2{
      bottom: 16em;
      left: -2em;
      width: 3.4375em;
    }
    &__leaf{
      display: none;
    }
    &__leaf3{
      width: 4.4375em;
      right: 0;
      top: 7.25em;
    }
  }
}
