.air-datepicker{
  width: 25.6875em;
  font-size: 1em;
  max-width: calc(100vw - 3.25em);
  &:before,
  &:after{
    display: none;
  }
  &--content{
    padding: 0;
  }
}

.air-datepicker-nav{
  height: 3.125em;
  padding: 0;
  &--title{
    font-size: 1.125em;
    font-weight: 600;
  }
  &--action{
    border-radius: 0;
    @include center;
    width: 3.125em;
    &:hover{
      background-color: $primary;
      path{
        stroke: #fff;
      }
    }
  }
}

.air-datepicker-body--day-names{
  background: #FCF7F7;
  margin: 0;
  height: 3.125em;
  padding: 0 1.28em;
}

.air-datepicker-body--day-name{
  font-size: .9375em;
  text-transform: unset;
  font-weight: 600;
  color: #3A3434;
}

.air-datepicker-body--cells.-days-{
  padding: 1.25em 1.28em;
  grid-template-columns: repeat(7,calc(100% / 7 - 3em / 7));
  grid-template-rows: unset;
  grid-auto-rows: unset;
  gap: 0 .5em;
}

.air-datepicker-cell{
  font-size: 1em;
  height: 2.75em;
  border-radius: 50%;
  color: #464646;
  &.-selected-,
  &.-current-{
    background-color: $primary !important;
    color: #fff;
  }
  &.-year-,
  &.-month-{
    border-radius: 5.625em;
  }
}

.ui-timepicker-standard{
  font-size: 1em;
  z-index: 10 !important;
  font-family: $font1;
  width: 14.2em !important;
  transform: translateX(-9.6em);
  border: 1px solid #F8F5F5;
  padding: 0;
}

.ui-timepicker-viewport,
.ui-timepicker{
  padding: 0;
  height: 25.0625em;
  width: 100% !important;
  @include scroll;
}

.ui-timepicker-standard a{
  border: none;
  display: flex;
  align-items: center;
  padding: 0 .94em;
  font-size: 1em;
  height: 3.5625em;
  cursor: pointer;
  &:not(:last-child){
    border-bottom: 1px solid #F8F5F5;
  }
  &.ui-state-hover{
    border: none;
    background-color: $primary;
    color: #fff;
  }
}


@media screen and (max-width: 576px){
  .air-datepicker{
    transform: translateX(-25%) !important;
  }
  .air-datepicker-global-container{
    //position: fixed !important;
    //top: 50% !important;
    //left: 50% !important;
    //transform: translate(-50%,-50%);
    //z-index: 100;
  }

  .air-datepicker-cell{
    height: 2em;
  }

  .ui-timepicker-standard{
    width: calc(100vw - 3.75em) !important;
  }

  .ui-timepicker-viewport,
  .ui-timepicker{
    padding: 0;
    height: 21.5em;
    width: 100% !important;
    @include scroll;
  }
}
