.conf{
  padding: 2.19em 0 4em;
  .text{
    margin-top: 1.875em;
    color: $black;
    ul{
      gap: .25em;
    }
    a{
      color: $primary;
      text-decoration: underline;
      font-weight: 700;
    }
  }
  &__hint{
    padding: 1.25em 1.56em;
    border-radius: 0.5em;
    background: #F8F8F8;
    .text{
      margin: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .conf{
    padding: 1.25em 0 2.5em;
    &__hint{
      padding: .94em;
    }
  }
}


@media screen and (max-width: 576px){
  .conf{
    &__title{
      .text43{
        font-size: 1.5em;
      }
      &:before,
      &:after{
        display: none;
      }
    }
  }
}
